import React, {memo, useCallback, useState} from 'react';
import {LayoutChangeEvent, Text, TextStyle} from 'react-native';
import {getTruncationIndex} from '@/utils/text';
import {stripSpecialCharacters, styled} from '@true/core';
import {Fonts} from '@true/core';
import {setIndexMap, useIndex} from '@/zustand/indexSeeMore';
import {formatPlainText} from '@/utils';

interface Props {
  children: any;
  numberOfLines: number;
  linkColor?: string;
  linkPressedColor?: string;
  linkStyle?: TextStyle;
  seeMoreText?: string;
  seeLessText?: string;
  style?: TextStyle;
  id: number | string;
}

const SeeMore = memo(
  ({
    linkColor = '#2E75F0',
    seeMoreText = 'See more',
    style = {
      fontFamily: undefined,
      fontSize: 14,
      fontWeight: '300',
    },
    linkStyle,
    children,
    numberOfLines,
    id,
  }: Props) => {
    // const [isLinkPressed, setLinkPressed] = useState(false);
    // const [isShowingMore, setShowingMore] = useState(false);
    const containerWidthToTruncationIndexMap = useIndex(id);

    const [truncationIndex, setTruncationIndex] = useState<number | undefined>(
      containerWidthToTruncationIndexMap,
    );

    const findTruncationIndex = useCallback(
      async (containerWidth: number) => {
        if (
          containerWidthToTruncationIndexMap &&
          containerWidthToTruncationIndexMap
        ) {
          return containerWidthToTruncationIndexMap;
        }

        const {fontSize, fontFamily, fontWeight} = style;

        try {
          const truncationIndex = await getTruncationIndex(
            formatPlainText(children),
            numberOfLines,
            containerWidth,
            seeMoreText,
            fontSize || 16,
            fontFamily,
            fontWeight || '400',
          );

          // @ts-ignore
          // containerWidthToTruncationIndexMap.current = {
          //   // @ts-ignore
          //   ...containerWidthToTruncationIndexMap.current,
          //   [id]: truncationIndex,
          // };
          setIndexMap(truncationIndex, id);

          return truncationIndex;
        } catch (e) {
          console.log(e);
          return 0;
        }
      },
      [
        children,
        numberOfLines,
        seeMoreText,
        style,
        containerWidthToTruncationIndexMap,
        id,
      ],
    );

    const findAndUpdateTruncationIndex = useCallback(
      async (containerWidth: number) => {
        const truncationIndex = await findTruncationIndex(containerWidth);
        setTruncationIndex(truncationIndex);
      },
      [findTruncationIndex],
    );

    const onLayout = useCallback(
      (e: LayoutChangeEvent) => {
        if (truncationIndex) return;

        // e.persist() keeps the original synthetic event intact
        e.persist();
        findAndUpdateTruncationIndex(e.nativeEvent.layout.width).then();
      },
      [findAndUpdateTruncationIndex, truncationIndex],
    );

    // const collapse = () => {
    //   return new Promise(resolve => {
    //     this.setState({isShowingMore: false}, () => resolve());
    //   });
    // }

    // const handleLinkPressed = () => {
    //   setLinkPressed(true);
    // };
    //
    // const handleLinkTerminated = () => {
    //   setLinkPressed(false);
    // };
    //
    // const handleLinkReleased = () => {
    //   setLinkPressed(false);
    //   setShowingMore(prev => !prev);
    // };

    // const panResponder = useRef(
    //   PanResponder.create({
    //     onStartShouldSetPanResponder: () => true,
    //     onPanResponderTerminationRequest: () => true,
    //     onPanResponderGrant: handleLinkPressed,
    //     onPanResponderTerminate: handleLinkTerminated,
    //     onPanResponderRelease: handleLinkReleased,
    //   }),
    // ).current;

    const renderSeeMoreSeeLessLink = useCallback(() => {
      const isTruncable = truncationIndex && truncationIndex < children.length;

      if (!isTruncable) {
        return null;
      }

      return (
        <Text>
          {
            <SText
              // @ts-ignore
              style={style}>
              ...
            </SText>
          }
          <SSubText
            // @ts-ignore
            style={linkStyle}>{` ${seeMoreText}`}</SSubText>
        </Text>
      );
    }, [children, linkStyle, seeMoreText, style, truncationIndex]);

    return (
      <SText testID="SeeMore" onLayout={onLayout} numberOfLines={numberOfLines}>
        <SText
          // @ts-ignore
          style={style}>
          {stripSpecialCharacters(
            children.slice(0, truncationIndex ? truncationIndex : 0),
          )}
        </SText>
        {renderSeeMoreSeeLessLink()}
      </SText>
    );
  },
);

const SText = styled.Text`
  font-size: 16px;
  line-height: 24px;
  font-family: ${Fonts.Regular};
  color: ${p => p.theme.grey1};
`;

const SSubText = styled.Text`
  font-size: 16px;
  line-height: 24px;
  font-family: ${Fonts.Regular};
  color: ${p => p.theme.grey2};
`;

export default SeeMore;
