import {Fonts} from '@true/core';
import {HTMLRenderer} from '@true/ui';
import {memo, useCallback} from 'react';
import {styled} from '@true/core';
import {ButtonHTMLExtend} from '@/components/ButtonHTMLExtend';
import { navigateToDetailPostScreen} from '@/utils';
import {useUpdates} from '@/store/updates';
import Animated, {
  useAnimatedStyle,
  useSharedValue,
} from 'react-native-reanimated';
import {LayoutChangeEvent, Pressable, View} from 'react-native';
import {MAX_HEIGHT_POST} from '@/components/PostThumbnail/index';

interface CoreValuePostProps {
  isInFeed: boolean;
  id: number;
}

export const CoreValuePost = memo(function (props: CoreValuePostProps) {
  const {isInFeed, id} = props;
  const height = useSharedValue(0);
  const data = useUpdates(id);

  const onNavigateToDetail = useCallback(() => {
    navigateToDetailPostScreen({
      id: data?.attachment.id,
      user_id: data?.user_id || 0,
      time: data?.since || 0,
      hid: data?.hid || '',
    });
  }, []);

  const onLayout = useCallback((e: LayoutChangeEvent) => {
    const _height = e.nativeEvent?.layout?.height;
    height.value = _height;
  }, []);

  const animatedStyle = useAnimatedStyle(() => {
    return {
      maxHeight: isInFeed ? MAX_HEIGHT_POST : undefined,
    };
  });
  return (
    <SContainer onPress={onNavigateToDetail}>
      <Animated.View style={[animatedStyle, {overflow: 'hidden'}]}>
        <View onLayout={onLayout}>
          <SContentWrapper>
            <STitle>{`${data?.name}`}</STitle>
            <View
              style={{
                overflow: 'hidden',
                backgroundColor: 'white',
                paddingTop: 4,
              }}>
              <HTMLRenderer
                htmlContent={data?.attachment.content || ''}
                defaultTextProps={{
                  style: {
                    fontSize: 14,
                    lineHeight: 20,
                    color: '#242424',
                  },
                }}
              />
            </View>
          </SContentWrapper>
        </View>
      </Animated.View>
      {isInFeed ? (
        <ButtonHTMLExtend
          onPress={onNavigateToDetail}
          height_content={height}
        />
      ) : null}
    </SContainer>
  );
});
const SContainer = styled(Pressable)``;

const STitle = styled.Text`
  font-family: ${Fonts.Regular};

  font-weight: 700;
  font-size: 16px;
  line-height: 24px;
  color: #242424;
`;
const SImage = styled.Image`
  width: 100%;
  height: 160px;
  max-height: 160px;
  background-color: #2a91d6;
`;
const SImageView = styled.View`
  width: 100%;
  height: 160px;
  max-height: 160px;
  padding: 8px 0;
`;
const SContentWrapper = styled.View`
  padding: 16px 12px;
  border-width: 1px;
  border-color: #e3e3e3;
  border-radius: 8px;
`;
