import React, {memo, useMemo} from 'react';
import {Fonts, styled} from '@true/core';
import {useUpdates} from '@/store/updates';
import {ReactionList} from '@/components/ReactionList';
import {View} from 'react-native';

interface Props {
  id: number;
}

export const PostStats = memo(({id}: Props) => {
  const dataPost = useUpdates(id);

  const stats = useMemo(
    () =>
      dataPost?.attachment
        ? {
            likes: dataPost.attachment?.reactions?.length || 0,
            comments: dataPost.attachment?.comments?.length || 0,
          }
        : dataPost?.stats,
    [dataPost],
  );

  const reactions = useMemo(
    () =>
      dataPost?.attachment
        ? dataPost?.attachment.reactions
        : dataPost?.reactions,
    [dataPost],
  );

  if (stats?.likes > 0 || stats?.comments > 0) {
    return (
      <SSectionWrapper>
        {stats?.likes > 0 ? (
          <ReactionList
            withNumber={false}
            likes={stats?.likes}
            reactions={reactions}
          />
        ) : (
          <View />
        )}
        {stats?.comments > 0 ? (
          <STextComments>{`${stats?.comments} comments`}</STextComments>
        ) : (
          <View />
        )}
      </SSectionWrapper>
    );
  }

  return <SSectionWrapper />;
});

const SSectionWrapper = styled.View`
  background-color: ${p => p.theme.backgroundColor};
  padding: 8px 16px;
  align-items: center;
  flex-direction: row;
  justify-content: space-between;
`;

const STextComments = styled.Text`
  font-family: ${Fonts.Regular};
  font-size: 14px;
  line-height: 20px;
  color: ${p => p.theme.neutral2};
`;
