import {memo, useCallback, useEffect} from 'react';
import {goBack, styled, useAutoToastError, useBoolean} from '@true/core';
import {CustomHeader} from '@/components/CustomHeader';
import {uploadImage} from '@/utils/helper';
import {
  IEditCompany,
  initEditCompany,
  requestEditCompanyInfo,
} from '@/store/settings/function';
import {useSetting} from '@/store/settings';
import {DiscardDialog} from '@/components/DiscardDialog';
import {
  FormScroll,
  FullScreenWrapper,
  MyKeyboardAvoidingView,
  UIDateTimeInput,
  UIInput,
} from '@true/ui';
import {MyInputLogo} from '@/screens/CreateEventScreen/MyInputImage';
import {AnimatedBottomSpace} from '@/components/AnimatedBottomSpace';
import {ImageDTO} from '@/components/CameraRollPicker/useGallery';
import {useAsyncFn} from '@/hooks/useAsyncFn';
import {MyInputImage} from '@/screens/CreateEventScreen/MyInputLogo';
import {runOnUI, useSharedValue} from 'react-native-reanimated';
import {CheckboxWithText} from '@/components/CheckboxWithText';

const EditCompanyScreen = memo(function CompanyScreen() {
  const setting = useSetting();
  const sysInfo = setting?.sysinfo;
  const [visibleDialog, showDialog, hideDialog] = useBoolean();

  const params = useSharedValue<IEditCompany>(initEditCompany);

  useEffect(() => {
    let _params = {
      ...initEditCompany,
    };
    if (sysInfo) {
      _params = {
        title: sysInfo.title,
        logo: sysInfo.logo,
        image: sysInfo.image,
        address: sysInfo.address,
        vision: sysInfo.vision,
        founded: sysInfo.founded,
        mv_sticky: sysInfo.config.mv_sticky === 0 ? '' : 'on',
      };
    }
    params.value = _params;
  }, [sysInfo, params]);

  const onGoBack = useCallback(() => {
    console.log('aa');
    goBack();
  }, []);

  const [{loading, error: createError}, onPressSubmit] =
    useAsyncFn(async () => {
      await requestEditCompanyInfo(params.value);
      goBack();
    }, []);
  useAutoToastError(createError);

  const onSelectBackground = useCallback(async (image: ImageDTO) => {
    const imageUrl = await uploadImage(image);
    onChangeValue('image', imageUrl);
  }, []);

  const onSelectLogo = useCallback(async (image: ImageDTO) => {
    const imageUrl = await uploadImage(image);
    onChangeValue('logo', imageUrl);
  }, []);

  const onChangeValue = useCallback(
    (key: string, value: any) => {
      runOnUI((_key: string, _value) => {
        'worklet';

        params.value = {
          ...params.value,
          [_key]: _value,
        };
      })(key, value);
    },
    [params],
  );

  return (
    <FullScreenWrapper>
      <MyKeyboardAvoidingView>
        <CustomHeader
          title={'Company'}
          isSearch={false}
          isBack
          onCreate={onPressSubmit}
          okText={'Save'}
        />
        <FormScroll>
          <MyInputImage
            onSelectedImage={onSelectBackground}
            image={params.value.image}
          />
          <MyInputLogo
            onSelectedImage={onSelectLogo}
            image={params.value.image}
          />
          <SWrapperInput>
            <UIInput.Text
              title={'Company name'}
              keyName={'title'}
              params={params}
              onChangeValue={onChangeValue}
            />
          </SWrapperInput>
          <SWrapperInput>
            <UIInput.Text
              title={'Short description'}
              keyName={'title'}
              params={params}
              placeholder={'Enter short description'}
              defaultValue={''}
            />
          </SWrapperInput>
          <SWrapperInput>
            <UIInput.Text
              title={'Company address'}
              keyName={'address'}
              params={params}
              defaultValue={''}
            />
          </SWrapperInput>
          <SWrapperInput>
            <UIDateTimeInput
              title={'Founded'}
              keyName={'founded'}
              params={params}
              mode={'datetime'}
            />
          </SWrapperInput>
          <SWrapperInput>
            <STextBold>Mission and Vision</STextBold>
          </SWrapperInput>
          <SWrapperInput>
            <UIInput.Text
              title={'Vision'}
              required
              keyName={'vision'}
              params={params}
              defaultValue={''}
            />
          </SWrapperInput>
          <SWrapperInput>
            <UIInput.Text
              title={'Mission'}
              required
              keyName={'mission'}
              params={params}
              defaultValue={''}
            />
          </SWrapperInput>
          <SWrapperInput>
            <STextGrey>Display preference</STextGrey>
          </SWrapperInput>
          <CheckboxWithText
            params={params}
            keyName={'mv_sticky'}
            title={'Make missions and visions highlighted on the homepage'}
          />
          <AnimatedBottomSpace />
        </FormScroll>
      </MyKeyboardAvoidingView>

      <DiscardDialog
        title={'Discard change'}
        content={
          'You have made changes to this item that have not been saved. If you leave this page, your changes will be lost.'
        }
        visible={visibleDialog}
        onClose={hideDialog}
      />
    </FullScreenWrapper>
  );
});

const SContainer = styled.View``;

export default EditCompanyScreen;

const SWrapperInput = styled.View`
  margin: 8px 16px 0 16px;
`;
const STextBold = styled.Text`
  font-weight: 700;
  font-size: 16px;
  line-height: 24px;
`;
const STextGrey = styled.Text`
  font-weight: 400;
  font-size: 14px;
  line-height: 20px;
  color: rgba(1, 18, 34, 0.6);
`;
