import {memo, useCallback} from 'react';
import {useSetStatusBar} from '@/hooks/useSetStatusBar';
import {Fonts, IC_SEARCH, styled, useMe} from '@true/core';
import Animated, {
  interpolate,
  SharedValue,
  useAnimatedStyle,
  useDerivedValue,
} from 'react-native-reanimated';
import {useAnimatedSafeAreaInsets} from 'react-native-safe-area-context';
import {Avatar, UIButton} from '@true/ui';
import FastImage from 'react-native-fast-image';
import {IC_OUTLINED_LEFT, IC_OUTLINED_EDIT_SQUARE} from '@true/asset';
import {Dimensions} from 'react-native';

const {width: DWidth} = Dimensions.get('screen');

const HEIGHT_HEADER = 44;
const AVATAR_SIZE = 28;
const ICON_SIZE = 24;

interface Props {
  animatedOffsetY?: SharedValue<number>;
  isSearch?: boolean;
  onSearch?: () => void;
  onPress?: () => void;
  onLeftPress?: () => void;
}

export const HomeHeader = memo(
  ({animatedOffsetY, isSearch, onSearch, onPress, onLeftPress}: Props) => {
    const {aTop} = useAnimatedSafeAreaInsets();
    const me = useMe();

    const _animatedOffsetY = useDerivedValue(() => {
      if (!animatedOffsetY) return 0;

      if (animatedOffsetY.value > 44) return 44;
      if (animatedOffsetY.value < 0) return 0;

      return animatedOffsetY.value;
    });

    const animatedStyle = useAnimatedStyle(() => {
      const sHeight = isSearch
        ? HEIGHT_HEADER * 2 + 4 + 8 + aTop.value
        : HEIGHT_HEADER + aTop.value;

      return {
        paddingTop: aTop.value,
        height: interpolate(
          _animatedOffsetY.value,
          [0, HEIGHT_HEADER],
          [sHeight, HEIGHT_HEADER + aTop.value + 4],
        ),
      };
    }, [isSearch]);

    const animatedSearchStyle = useAnimatedStyle(() => {
      return {
        top: aTop.value + 44 - _animatedOffsetY.value,
        paddingTop: interpolate(
          _animatedOffsetY.value,
          [0, HEIGHT_HEADER],
          [8, 0],
        ),
        paddingBottom: interpolate(
          _animatedOffsetY.value,
          [0, HEIGHT_HEADER],
          [4, 0],
        ),
      };
    });

    const animatedSearchBtnStyle = useAnimatedStyle(() => ({
      width: interpolate(
        _animatedOffsetY.value,
        [0, 44],
        [DWidth - 16 * 2, DWidth - 16 * 4 - ICON_SIZE - ICON_SIZE],
      ),
    }));

    const animatedTitleStyle = useAnimatedStyle(() => ({
      opacity: _animatedOffsetY.value < HEIGHT_HEADER ? 1 : 0,
    }));

    const animatedAvatarStyle = useAnimatedStyle(() => ({
      top: aTop.value + (HEIGHT_HEADER - ICON_SIZE) / 2,
    }));

    const animatedIconStyle = useAnimatedStyle(() => ({
      top: aTop.value + (HEIGHT_HEADER - ICON_SIZE) / 2,
    }));

    useSetStatusBar();

    return (
      <SWrapper style={animatedStyle}>
        <SAvatarContainer style={animatedAvatarStyle} onPress={onLeftPress}>
          {/*<Avatar userKey={me?.id || ''} size={AVATAR_SIZE} disableClick />*/}
          <SIcon source={IC_OUTLINED_LEFT} />
        </SAvatarContainer>
        <SEditButton onPress={onPress} style={animatedIconStyle} />
        <SContainer>
          <STitle style={animatedTitleStyle}>Inside</STitle>
        </SContainer>
        {isSearch ? (
          <SSearchWrapper style={animatedSearchStyle}>
            <SSearch style={animatedSearchBtnStyle} onPress={onSearch} />
          </SSearchWrapper>
        ) : null}
      </SWrapper>
    );
  },
);

const SAvatarContainer = styled(UIButton)`
  position: absolute;
  left: 16px;
  z-index: 1000;
`;

const SEditButton = styled(UIButton).attrs(p => ({
  source: IC_OUTLINED_EDIT_SQUARE,
  imageSize: ICON_SIZE,
  imageStyle: {
    tintColor: p.theme.white,
  },
}))`
  position: absolute;
  right: 16px;
  z-index: 1000;
`;

const SWrapper = styled(Animated.View)`
  background-color: ${p => p.theme.primaryColor};
  justify-content: flex-start;
`;

const SContainer = styled.View`
  padding: 0 16px;
  height: ${HEIGHT_HEADER}px;
  justify-content: center;
  align-items: center;
  flex-direction: row;
`;

const STitle = styled(Animated.Text)`
  color: ${p => p.theme.white};
  font-size: 18px;
  line-height: 24px;
  font-family: ${Fonts.Bold};
`;

const SSearchWrapper = styled(Animated.View)`
  position: absolute;
  left: 0;
  right: 0;
  height: ${HEIGHT_HEADER}px;
  align-items: center;
  justify-content: center;
  background-color: transparent;
  z-index: 10;
`;

const SSearch = styled(UIButton).attrs(p => ({
  source: IC_SEARCH,
  imageSize: 20,
  imageStyle: {
    tintColor: p.theme.neutral3,
  },
  title: 'Search for the updates...',
  textStyle: {
    fontSize: 14,
    lineHeight: 20,
    fontFamily: Fonts.Regular,
    color: p.theme.neutral2,
    flex: 1,
  },
}))`
  padding: 8px 12px;
  background-color: ${p => p.theme.backgroundColor};
  width: ${DWidth - 16 * 2};
  border-radius: 8px;
  z-index: 999;
`;

const SIcon = styled(FastImage).attrs(p => ({
  tintColor: p.theme.white,
}))`
  width: ${ICON_SIZE}px;
  height: ${ICON_SIZE}px;
`;
