import create from 'zustand';
import shallow from 'zustand/shallow';

export interface activeUpdateState {
  type: string | undefined;
  id: number;
  user_id: number | undefined;
  hid: string | undefined;
}

const activeUpdateStore = create<{activeUpdate: activeUpdateState}>(() => ({
  activeUpdate: {
    type: '',
    id: 0,
    user_id: 0,
    hid: '',
  },
}));

export const useActiveUpdate = () => {
  return activeUpdateStore(state => state.activeUpdate, shallow);
};
export const setActiveUpdate = (
  type: string | undefined,
  id: number,
  user_id: number | undefined,
  hid: string | undefined,
) => {
  return activeUpdateStore.setState({
    activeUpdate: {
      type: type,
      id: id,
      user_id: user_id,
      hid: hid,
    },
  });
};
