import React, {memo, useEffect} from 'react';
import {ActivityIndicator} from 'react-native';
import {
  AnimatedBottomSpace,
  DynamicHeader,
  MyKeyboardAvoidingView,
  ScreenWrapper,
  UIButton,
  UIText,
} from '@true/ui';
import {
  Fonts,
  goBack,
  styled,
  useAsyncFn,
  useAutoToastError,
  useNavigationParams,
} from '@true/core';
import {AvatarWithSelectChannel} from './AvatarWithSelectChannel';
import {FlexCenter} from '@/utils/helper';
import {StylePicker} from '@/screens/CreateStyledUpdateScreen/StylePicker';
import {ContentUpdate} from '@/screens/CreateStyledUpdateScreen/ContentUpdate';
import {
  CreateStyledUpdateProvider,
  useStyledUpdate,
} from '@/screens/CreateStyledUpdateScreen/provider';
import {BackgroundPicker} from '@/screens/CreateStyledUpdateScreen/BackgroundPicker';
import {requestCreatePost, requestEditPost} from '@/store/updates/function';
import {useUpdates} from '@/store/updates';
import {Buffer} from 'buffer';

export interface CreateStyledUpdateScreenParams {
  id?: number;
}

export const _CreateStyledUpdateScreen = memo(() => {
  const {
    selectedChannelId,
    selectedColor,
    selectedOpacity,
    image,
    content,
    cameraRollRef,
  } = useStyledUpdate();
  const {id} = useNavigationParams<CreateStyledUpdateScreenParams>();
  const data = useUpdates(id);

  useEffect(() => {
    if (data) {
      content.value = data.content;
      selectedChannelId.value = data.channel_id;
      const _image = JSON.parse(
        Buffer.from(data.image || '', 'base64').toString('utf-8'),
      );
      if (_image) {
        selectedColor.value = _image.color;
        image.background.value = _image.image;
        image.type.value = _image.type;
        selectedOpacity.value = _image.percent / 100;
      }
    }
  }, [data]);

  const [{error, loading}, onSubmit] = useAsyncFn(async () => {
    const params = {
      metatype: 'styled',
      channel_id: selectedChannelId.value,
      'image-type': image.type.value,
      'image-bg': image.background.value,
      'image-percent': selectedOpacity.value * 100,
      'image-color': selectedColor.value,
      content: content.value,
    };

    if (id) {
      await requestEditPost({...params, id} as any);
    } else {
      await requestCreatePost(params as any);
    }

    cameraRollRef?.current?.clearImages();
    goBack();
  }, [
    selectedChannelId,
    selectedColor,
    selectedOpacity,
    image,
    content,
    cameraRollRef,
    id,
  ]);

  useAutoToastError(error);

  return (
    <ScreenWrapper isGrey>
      <DynamicHeader
        title={'Create styled update'}
        rightComponent={
          <SSubmitBtn onPress={onSubmit}>
            {loading ? (
              <ActivityIndicator size={24} />
            ) : (
              <STitle>{id ? 'Update' : 'Create'}</STitle>
            )}
          </SSubmitBtn>
        }
      />
      <MyKeyboardAvoidingView>
        <SContainer>
          <SScrollView>
            <AvatarWithSelectChannel />
            <ContentUpdate />
            <BackgroundPicker />
            <FlexCenter>
              <StylePicker />
            </FlexCenter>
          </SScrollView>
          <AnimatedBottomSpace />
        </SContainer>
      </MyKeyboardAvoidingView>
    </ScreenWrapper>
  );
});

export const CreateStyledUpdateScreen = memo(() => {
  return (
    <CreateStyledUpdateProvider>
      <_CreateStyledUpdateScreen />
    </CreateStyledUpdateProvider>
  );
});

const SContainer = styled.View`
  flex: 1;
  margin-top: 8px;
`;

const SScrollView = styled.ScrollView``;

const SSubmitBtn = styled(UIButton).attrs(p => ({
  textStyle: {
    fontSize: 14,
    lineHeight: 24,
    fontFamily: Fonts.Regular,
    color: p.theme.white,
  },
}))`
  width: 94px;
  padding: 4px 16px;
  background-color: ${p => p.theme.primaryColor};
  margin-right: 16px;
  align-items: center;
  justify-content: center;
  border-radius: 8px;
  margin-left: 16px;
`;

const STitle = styled(UIText.ButtonMedium)`
  color: ${p => p.theme.white};
`;
