import constate from 'constate';
import {useSharedValue} from 'react-native-reanimated';
import {useMemo, useRef} from 'react';
import {CameraRollPickerRef} from '@true/ui/src/components/CameraRollPicker';
import {StyleColors} from '@/services/BgTemplateService';

const _provider = () => {
  const selectedChannelId = useSharedValue(0);
  const selectedColor = useSharedValue(StyleColors[0]);
  const selectedOpacity = useSharedValue(1);
  const content = useSharedValue('');
  const image = {
    type: useSharedValue(''),
    background: useSharedValue(''),
  };
  const cameraRollRef = useRef<CameraRollPickerRef>(null);

  return useMemo(
    () => ({
      selectedChannelId,
      selectedColor,
      selectedOpacity,
      image,
      cameraRollRef,
      content,
    }),
    [
      selectedChannelId,
      selectedColor,
      selectedOpacity,
      image,
      cameraRollRef,
      content,
    ],
  );
};

export const [CreateStyledUpdateProvider, useStyledUpdate] = constate(
  _provider,
  val => val,
);
