import React, {useRef} from 'react';
import {StatusBar} from 'react-native';
import {InitialState, useIsFocused, useRoute} from '@react-navigation/native';

export const useSetStatusBar = () => {
  const route = useRoute();
  const entry = useRef<any>();
  const isFocused = useIsFocused();

  React.useEffect(() => {
    if (isFocused) {
      // Get the current screen name
      const screenName = route.name;
      let stack: any = null;

      if (!entry) return;

      // Change the status bar based on the screen name
      switch (screenName) {
        case 'Home':
        case 'DetailEventScreen':
          stack = StatusBar.pushStackEntry({
            barStyle: 'light-content',
            animated: true,
          });
          break;
        default:
          stack = StatusBar.pushStackEntry({
            barStyle: 'dark-content',
            animated: true,
          });
          break;
      }

      return () => {
        stack && StatusBar.popStackEntry(stack);
      };
    }
  }, [route.name, entry, isFocused]);
};
