import {memo, useCallback, useState} from 'react';
import {styled} from '@true/core';
import {openCameraRollScreen} from '@/utils';
import {ImageDTO} from '@/components/CameraRollPicker/useGallery';

interface MyInputLogoProps {
  onSelectedImage: (image: ImageDTO) => void;
  image: string | undefined;
}

export const MyInputLogo = memo(function (props: MyInputLogoProps) {
  const {onSelectedImage, image} = props;
  const [imageUri, setImageUri] = useState('');

  const onSelect = useCallback((image: ImageDTO) => {
    setImageUri(image.url);
    onSelectedImage(image);
  }, []);
  const onPress = useCallback(() => {
    openCameraRollScreen({
      onSubmit: onSelect,
    });
  }, []);

  return (
    <SContainer onPress={onPress}>
      <SImageLogo source={{uri: imageUri ? imageUri : image}} />
    </SContainer>
  );
});
const SContainer = styled.TouchableOpacity`
  width: 100px;
  height: 100px;
  border-radius: 100px;
  padding: 2px;
  background-color: white;
  align-items: center;
  justify-content: center;
  margin-top: -50px;
  margin-left: 16px;
`;
const SImageLogo = styled.Image`
  width: 80px;
  height: 80px;
`;
