import {
  IC_FILLED_ANNOUNCEMENT,
  IC_FILLED_FOLDER_SPECIAL,
  IC_FILLED_EVENT,
  IC_FILLED_IDEA,
  IC_FILLED_FORWARD_TO_INBOX,
  IC_FILLED_LEADERBOARD,
  IC_FILLED_WELCOME,
  IC_FILLED_TOPIC,
} from '@true/asset';

class TypePost {
  getTypePostImage = (type: string) => {
    return {
      announcement: IC_FILLED_ANNOUNCEMENT,
      events: IC_FILLED_EVENT,
      update: '',
      idea: IC_FILLED_IDEA,
      topics: IC_FILLED_TOPIC,
      polls: IC_FILLED_LEADERBOARD,
      companycorevalues: IC_FILLED_FOLDER_SPECIAL,
      companyletters: IC_FILLED_FORWARD_TO_INBOX,
      styled: '',
      welcome: IC_FILLED_WELCOME,
    }[type];
  };

  getReactionText = (type: string) => {
    return {
      announcement: 'Announcement',
      events: 'Event',
      update: 'Update',
      idea: 'Idea',
      topics: 'Topic',
      polls: 'Poll',
      companycorevalues: 'Core value',
      companyletters: 'CEO Letters',
      styled: 'Update',
      welcome: 'Welcome',
    }[type];
  };
}

export default new TypePost();
