import React, {memo, useMemo} from 'react';
import {styled} from '@true/core';
import {Text, View} from 'react-native';
import {FlexCenter} from '@/utils/helper';
import {IC_OUTLINED_MORE_HORIZ} from '@true/asset';
import {AttendeeAvatarList} from '@/screens/EventScreen/AttendeeAvatarList';
import {BottomSheetModal} from '@gorhom/bottom-sheet';
import {MyBottomSheet} from '@true/ui';
import {useChannels} from '@/store/channels';
import {getUser} from '@true/core';
import moment from 'moment/moment';

const actionChannelRef = React.createRef<BottomSheetModal>();

const openActionChannelSetting = () => {
  actionChannelRef.current?.present();
};
const closeActionChannelSetting = () => {
  actionChannelRef.current?.close();
};
const handleSnapToIndex = (index: number) => {
  actionChannelRef.current?.snapToIndex(index);
};

export const SettingChannelThumbnail = memo(function SettingChannelThumbnail({
  id,
}: {
  id: number;
}) {
  const channel = useChannels(id);
  const snapPoints = useMemo(() => {
    return ['30%'];
  }, []);
  return (
    <SContainer>
      <SAvatarIcon>
        <STextIcon>GG</STextIcon>
        <StatusDot />
      </SAvatarIcon>
      <SBorderView>
        <View>
          <STitle>{channel?.name}</STitle>
          <STextGrey>{`Created by ${
            getUser(channel?.username || '')?.name
          } at ${moment((channel?.since || 0) * 1000).format(
            'hh:mm DD/MM/YYYY',
          )}`}</STextGrey>
          <FlexCenter>
            <STextGrey>{`Admin:    `}</STextGrey>
            <AttendeeAvatarList size={16} ids={channel?.owners} maxUsers={4} />
          </FlexCenter>
        </View>
        <SIcon onPress={openActionChannelSetting}>
          <SImage source={IC_OUTLINED_MORE_HORIZ} />
        </SIcon>
      </SBorderView>
      <MyBottomSheet ref={actionChannelRef} snapPoints={snapPoints}>
        <View>
          <SItemSheet>
            <Text>Edit</Text>
          </SItemSheet>
          <SItemSheet>
            <Text>Leave this channel</Text>
          </SItemSheet>
          <SItemSheet>
            <Text style={{color: '#CF1322'}}>Remove</Text>
          </SItemSheet>
        </View>
      </MyBottomSheet>
    </SContainer>
  );
});
const SContainer = styled.View`
  flex-direction: row;
  align-items: flex-start;
  padding: 20px 16px 0 16px;
`;

const SAvatarIcon = styled.View`
  align-items: center;
  justify-content: center;
  width: 32px;
  height: 32px;
  border-radius: 8px;
  background-color: #267cde;
  margin-top: 8px;
  margin-right: 16px;
`;
const STitle = styled.Text`
  font-weight: 500;
  font-size: 16px;
  line-height: 24px;
  color: #22313f;
`;
const STextGrey = styled.Text`
  font-weight: 400;
  font-size: 12px;
  line-height: 16px;
  color: rgba(1, 18, 34, 0.6);
`;
const SIcon = styled.TouchableOpacity`
  align-items: flex-start;
  justify-content: flex-start;
  width: 40px;
  height: 40px;
  transform: rotate(90deg);
`;

const SImage = styled.Image`
  tint-color: #666666;
  width: 16px;
  height: 16px;
`;
const STextIcon = styled.Text`
  font-weight: 500;
  font-size: 12px;
  line-height: 16px;
  color: #ffffff;
`;
const SBorderView = styled.View`
  flex-direction: row;
  align-items: flex-start;
  justify-content: space-between;
  padding-bottom: 20px;
  border-bottom-width: 1px;
  border-bottom-color: #e3e3e3;
  flex: 1;
`;
const StatusDot = styled.View`
  position: absolute;
  bottom: -5px;
  right: -5px;
  width: 16px;
  height: 16px;
  background-color: #05944f;
  border: 2px solid #ffffff;
  border-radius: 20px;
`;
const SItemSheet = styled.View`
  border-bottom-width: 1px;
  border-bottom-color: #e3e3e3;
  padding: 16px;
`;
