import moment from 'moment';

export const dateToNum = (date?: string | null) => {
  return date ? moment(date, 'DD/MM/YYYY').unix() : null;
};

export const numToDate = (num?: number | null) => {
  return num ? moment(num, 'X').format('DD/MM/YYYY') : '';
};

export const dateFormat = (ts?: number, _format?: string) => {
  return ts ? moment.unix(ts).format(_format || '') : '';
};
