import React, {memo, RefObject} from 'react';
import {HeaderPost} from '@/components/PostThumbnail/HeaderPost';
import {memoForwardRef, styled} from '@true/core';
import {ActionPostSection} from '@/components/PostThumbnail/ActionPostSection';
import {useUpdates} from '@/store/updates';
import {CommentPreview, PreviewCommentList, Comment} from '@true/ui';
import {AttachmentFile} from '@/components/AttachmentFile';
import {IAttachmentFile} from '@/components/AttachmentFile/type';
import {PostStats} from '@/components/PostThumbnail/PostStats';
import {PostContent} from '@/components/PostThumbnail/PostContent';
import {AttachmentFilePost} from '@/components/PostThumbnail/AttachmentFilePost';

interface PostThumbnailProps {
  isInFeed: boolean;
  id: number;
}

export const MAX_HEIGHT_POST = 400;

export const PostThumbnail = memoForwardRef(function (
  props: PostThumbnailProps,
  ref: RefObject<PreviewCommentList>,
) {
  const {isInFeed, id} = props;
  const dataPost = useUpdates(id);
  return (
    <CommentPreview.Provider
      appName={'inside'}
      type={'comment'}
      hid={dataPost?.attachment?.hid || dataPost?.hid || ''}
      token={dataPost?.attachment?.token || dataPost?.token || ''}>
      <SContainer>
        <HeaderPost
          type={dataPost?.truetype}
          user_id={dataPost?.user_id}
          channel_id={dataPost?.channel_id}
          time={dataPost?.since}
          isInFeed={isInFeed}
          id={id}
          hid={dataPost?.hid || ''}
        />
        <PostContent id={id} isInFeed={isInFeed} />
        <AttachmentFilePost
          files={
            dataPost?.attachment ? dataPost.attachment.files : dataPost?.files
          }
        />
        <PostStats id={id} />
        <ActionPostSection
          listSeens={dataPost?.seens}
          reactions={
            dataPost?.attachment
              ? dataPost?.attachment.reactions
              : dataPost?.reactions
          }
          isPinned={dataPost?.pinned == 1}
          id={dataPost?.id || 0}
          type={dataPost?.truetype || 'update'}
        />
      </SContainer>
      {/*<CommentPreview.List maxNum={3} />*/}
    </CommentPreview.Provider>
  );
});
const SContainer = styled.View`
  padding-top: 12px;
  background-color: white;
  margin-bottom: 8px;
`;
