import {memo, useCallback, useMemo} from 'react';
import Animated, {
  SharedValue,
  useAnimatedStyle,
  useSharedValue,
} from 'react-native-reanimated';
import {Image, TouchableOpacity, ViewStyle} from 'react-native';
import {styled} from '@true/core';
import Metrics from '@/assets/Metrics';
import {usePostImage} from '@/components/PostThumbnail/postImageProvider';
import FastImage from 'react-native-fast-image';

interface Props {
  image: string;
  style?: ViewStyle;
  imageWidth?: SharedValue<number>;
  onPressImage: (index: number) => void;
  index: number;
}

export const AttachmentImage = memo(
  ({image, imageWidth, style, onPressImage, index}: Props) => {
    const imageSize = {
      width: useSharedValue(1),
      height: useSharedValue(1),
    };
    const {imageLayout} = usePostImage();

    const source = useMemo(() => {
      if (image) {
        Image.getSize(image, (width, height) => {
          imageSize.height.value = height;
          imageSize.width.value = width;
          imageLayout.value = {
            ...imageLayout.value,
            [image]: {
              w: width,
              h: height,
            },
          };
        });

        return {uri: image};
      }

      return null;
    }, [image, imageLayout]);

    const animatedImageStyle = useAnimatedStyle(() => {
      const _width =
        imageWidth && imageWidth.value > 0
          ? imageWidth.value
          : Metrics.screen.width;

      return {
        width: _width,
        height:
          ((imageSize.height.value || imageLayout.value[image || '']?.h || 1) *
            _width) /
          (imageSize.width.value || imageLayout.value[image || '']?.w || 1),
      };
    }, [image]);

    const onPress = useCallback(() => {
      onPressImage(index);
    }, [onPressImage]);

    if (!image || !source) {
      return null;
    }

    return (
      <TouchableOpacity onPress={onPress}>
        <SImageView style={[animatedImageStyle, style]}>
          <SImage source={source} resizeMode={'contain'} />
        </SImageView>
      </TouchableOpacity>
    );
  },
);

const SImage = styled(FastImage)`
  width: 100%;
  height: 100%;
`;

const SImageView = styled(Animated.View)`
  overflow: hidden;
  background-color: ${p => 'transparent'};
  align-items: center;
  justify-content: center;
`;
