import {IPollConfig} from '@/store/updates/type';
import {useCallback, useEffect, useMemo, useState} from 'react';
import moment from 'moment/moment';
import {
  runOnJS,
  useFrameCallback,
  useReducedMotion,
  useSharedValue,
} from 'react-native-reanimated';

export const useShouldShowSubmitButton = (config: IPollConfig, youVoted?: boolean) => {
  const [, _setState] = useState(0);
  const nowInSecond = Number(moment().format('X'));

  // auto re-render when start time come
  useEffect(() => {
    if (!(config.has_stime && nowInSecond < Number(config.stime))) {
      return () => {};
    }
    const timeout = Number(config.stime) - nowInSecond;
    const _timeout = setTimeout(() => {
      _setState(state => state + 1);
    }, timeout * 1000);
    return () => {
      clearTimeout(_timeout);
    };
  }, [config, nowInSecond]);

  // auto re-render when end time come
  useEffect(() => {
    if (!(config.has_etime && nowInSecond > Number(config.etime))) {
      return () => {};
    }
    const timeout = Number(config.etime) - nowInSecond;
    const _timeout = setTimeout(() => {
      _setState(state => state + 1);
    }, timeout * 1000);
    return () => {
      clearTimeout(_timeout);
    };
  }, [config, nowInSecond]);

  if (config.has_stime && nowInSecond < Number(config.stime)) return false;

  if (config.has_etime && nowInSecond > Number(config.etime)) return false;

  if (!youVoted) return true;

  return Boolean(Number(config.allow_update));
};
