import React, { memo, useCallback, useRef, useState } from "react";
import { Fonts, styled } from "@true/core";
import useAutoAlertError from "@/hooks/useAutoToastError";
import SimpleToast from "react-native-simple-toast/index";
import { useAsyncFn } from "@/hooks/useAsyncFn";
import { translate } from "@/hooks/useLanguage";
import { IPoll } from "@/store/updates/type";
import { IC_OUTLINED_ADD } from "@true/asset";
import { requestAddPollOptions } from "@/store/updates/function";
import { UIButton } from "@true/ui";
import { BottomSheetModal } from "@gorhom/bottom-sheet";
import { AddPollSheet } from "@/components/PostThumbnail/PollPost/screens/AddPollSheet";

export const AddPollOption = memo(function AddPollOption({
                                                           poll
                                                         }: {
  poll: IPoll;
}) {
  const [newName, setNewName] = useState("");
  const pollRef = useRef<BottomSheetModal>(null);

  const [{ loading, error }, onSubmit] = useAsyncFn(
    async (name: string) => {
      await requestAddPollOptions({
        name: name,
        id: poll.id,
        channel_id: poll.channel_id
      });

      SimpleToast.show("Add poll option successfully");
      pollRef.current?.dismiss();
    },
    [poll]
  );

  useAutoAlertError(error);

  const onPress = useCallback(async () => {
    if (!newName) {
      SimpleToast.show("Option name is required");
      return;
    }
    onSubmit(newName).then();
  }, [onSubmit, newName]);

  return (
    <>
      <Wrapper onPress={() => {
        pollRef.current?.present();
      }}>
        <AddOptionText>{translate("Add more poll option...")}</AddOptionText>
      </Wrapper>
      <AddPollSheet
        onChangeText={setNewName}
        value={newName}
        onPress={onPress}
        loading={loading}
        ref={pollRef}
      />
    </>
  );
});

export default AddPollOption;

const AddOptionText = styled.Text`
  font-size: 14px;
  line-height: 20px;
  color: ${p => p.theme.neutral2};
  font-family: ${Fonts.Regular};
  margin-left: 8px;
`;

const Wrapper = styled(UIButton).attrs(p => ({
  source: IC_OUTLINED_ADD,
  imageSize: 24,
  imageStyle: {
    tintColor: p.theme.neutral2
  }
}))`
  flex-direction: row;
  align-items: center;
  padding: 0 16px;
  min-height: 52px;
  border-radius: 8px;
  border: 1px solid ${p => p.theme.neutral5};
  margin-bottom: 8px;
`;
