import React, {memo, useEffect} from 'react';
import {ActivityIndicator} from 'react-native';
import {Colors, styled, useTheme, useZustand} from '@true/core';
import Animated, {
  useAnimatedProps,
  useAnimatedStyle,
  withTiming,
} from 'react-native-reanimated';

const [useVisible, getVisible, setVisible] = useZustand(false);

const LoadingModal = memo(({isVisible}: {isVisible: boolean}) => {
  useEffect(() => {
    setVisible(isVisible);
    return () => setVisible(false);
  }, [isVisible]);
  return null;
});

export const LoadingModalScreen = memo(() => {
  const isVisible = useVisible();
  const theme = useTheme();

  const animatedProps = useAnimatedProps(() => ({
    pointerEvents: isVisible ? 'auto' : 'none',
  }));
  const animatedStyle = useAnimatedStyle(
    () => ({
      opacity: isVisible ? withTiming(1) : withTiming(0),
    }),
    [isVisible],
  );
  return (
    <SLoadingView style={animatedStyle} animatedProps={animatedProps as any}>
      <SBg />
      <SLoadingIcon>
        <ActivityIndicator size={24} color={theme.primaryColor} />
      </SLoadingIcon>
    </SLoadingView>
  );
});

const SLoadingView = styled(Animated.View)`
  position: absolute;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
  z-index: 99;
`;

const SBg = styled.View`
  position: absolute;
  width: 100%;
  height: 100%;
  background-color: ${Colors.neutral1};
  opacity: 0.3;
`;

const SLoadingIcon = styled.View`
  width: 64px;
  height: 64px;
  justify-content: center;
  align-items: center;
  background-color: ${Colors.white};
  border-radius: 10px;
`;

export default LoadingModal;
