import {Fonts} from '@true/core';
import React, {memo, useCallback, useRef} from 'react';
import {styled} from '@true/core';
import {Dimensions, FlatList, View} from 'react-native';
import {FlexCenter} from '@/utils/helper';
import {IC_FILLED_PUSH_PIN} from '@true/asset';
import {globalStyles} from '@/utils/globalStyle';
import {FlashList} from '@shopify/flash-list';
import {UpdateSkeleton} from '@/components/SkeletonPlaceholder/UpdateSkeleton';
import {PinnedPostThumbnail} from '@/screens/HomeScreen/PinnedPostThumbnail';
import {UIText} from '@true/ui';
import {PinnedPostFooter} from '@/screens/HomeScreen/PinnedPostFooter';

interface PinnedUpdatesProps {
  listIds: any[];
}

export const PinnedUpdates = memo(function (props: PinnedUpdatesProps) {
  const {listIds} = props;
  const flashListRef = useRef<FlashList<any>>(null);

  const renderItem = useCallback(({item, index}: any) => {
    return <PinnedPostThumbnail id={item} key={index + item + `home_pinned`} />;
  }, []);

  return (
    <SContainer>
      <SContentHeader>
        <FlexCenter>
          <SIcon style={globalStyles.mr8}>
            <SImage source={IC_FILLED_PUSH_PIN} />
          </SIcon>
          <STextPinned>Pinned updates</STextPinned>
        </FlexCenter>
      </SContentHeader>
      <FlatList
        horizontal
        pagingEnabled
        disableIntervalMomentum
        snapToInterval={itemWidth}
        showsHorizontalScrollIndicator={false}
        data={listIds}
        extraData={listIds}
        renderItem={renderItem}
        keyExtractor={keyExtractor}
        onEndReachedThreshold={1}
        ListEmptyComponent={<UpdateSkeleton />}
        nestedScrollEnabled={true}
        // estimatedItemSize={600}
        removeClippedSubviews={true}
        ListFooterComponent={<PinnedPostFooter />}
      />
    </SContainer>
  );
});
const keyExtractor = (item: any) => {
  return `${item}+pinnedupdates`;
};
const SContainer = styled.View`
  padding-bottom: 12px;
  background-color: ${p => p.theme.backgroundColor};
`;
const SContentHeader = styled.View`
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  padding: 12px 16px;
`;
const STextPinned = styled(UIText.HeaderMedium)`
  color: ${p => p.theme.neutral1};
`;
const SIcon = styled.View`
  width: 24px;
  height: 24px;
  align-items: center;
  justify-content: center;
`;
const SImage = styled.Image`
  width: 24px;
  height: 24px;
`;
const width = Dimensions.get('window').width;
const itemWidth = (width / 3) * 2 - 32;
