import React, {RefObject} from 'react';
import {BottomSheetModal, BottomSheetView} from '@gorhom/bottom-sheet';
import {AnimatedBottomSpace} from '@/components/AnimatedBottomSpace';
import Animated from 'react-native-reanimated';
import {MyBottomSheet, useAnimatedSnapPoints} from '@true/ui';
import BottomSheetHeader from '@/components/BottomSheets/BottomSheetHeader';
import {memoForwardRef} from '@true/core';

interface IMenuItem {
  label: string;
  value: string;
}

export const BottomSheetMenu = memoForwardRef(
  (
    {
      name,
      items,
      titleHeader,
      renderMenuItem,
      snapPoints,
    }: {
      items: IMenuItem[];
      name: string;
      titleHeader?: string;
      renderMenuItem: (item: IMenuItem, index: number) => JSX.Element;
      snapPoints?: Array<string | number>;
    },
    ref: RefObject<BottomSheetModal>,
  ) => {
    const {animatedSnapPoints, onViewLayout} = useAnimatedSnapPoints();

    return (
      <MyBottomSheet
        ref={ref}
        name={name}
        snapPoints={snapPoints || animatedSnapPoints}>
        <BottomSheetView onLayout={onViewLayout('headers')}>
          {titleHeader ? (
            <BottomSheetHeader
              title={titleHeader}
              onPress={() => {
                ref?.current?.dismiss();
              }}
            />
          ) : null}
          <Animated.View style={{overflow: 'scroll'}}>
            {items.map((item, index) => (
              <>{renderMenuItem(item, index)}</>
            ))}
          </Animated.View>
          <AnimatedBottomSpace />
        </BottomSheetView>
      </MyBottomSheet>
    );
  },
);
