import React, {memo, useCallback, useMemo} from 'react';
import {
  DynamicCheckBox,
  FormScroll,
  MyDateTimeInput,
  MySelectorInput,
  MyUserPickerInput,
  UIBottomButton,
  UIInput,
} from '@true/ui';
import {styled, translate, useTheme} from '@true/core';
import {dateFormat, dateToNum} from '@/utils/date';
import {goBack} from '@true/core';
import {Fonts} from '@true/core';
import {AnimatedBottomSpace} from '../AnimatedBottomSpace';
import {FlexCenter} from '@/utils/helper';
import {getChannels, useChannelsByQuery} from '@/store/channels';

export type IInputFilterType =
  | 'channel'
  | 'user'
  | 'storage'
  | 'receiver'
  | 'doctype'
  | 'filter'
  | 'sort'
  | 'created_by'
  | 'folder'
  | 'created_date'
  | 'effective_date'
  | 'expired_date'
  | 'only_answered';

interface Props {
  items: IInputFilterType[];
  params: any;
  setParams: (params: any) => any;
  defaultParams: any;
  onSubmit: (params: any) => any;
}

export const FilterComp = memo((props: Props) => {
  const {items, params, setParams, defaultParams, onSubmit} = props;

  const _onSubmit = useCallback(() => {
    onSubmit(params);
    goBack();
  }, [params, onSubmit]);

  const onReset = useCallback(() => {
    onSubmit(defaultParams);
    goBack();
  }, [defaultParams, onSubmit]);

  const theme = useTheme();

  return (
    <>
      <FormScroll>
        {items.map(itemType => (
          <SItemContainer>
            <ItemInput type={itemType} params={params} setParams={setParams} />
          </SItemContainer>
        ))}
      </FormScroll>
      <UIButtonWrapper>
        <UIBottomButton
          buttons={[
            {
              title: 'Reset',
              onPress: onReset,
              textColor: theme.primaryColor,
              // disabled: value === undefined,
              backgroundColor: theme.primaryColor + '20',
            },
            {
              title: 'Apply',
              onPress: _onSubmit,
              textColor: '#FFFFFF',
              backgroundColor: theme.primaryColor,
              // disabled: value === undefined,
            },
          ]}
        />
      </UIButtonWrapper>
      <AnimatedBottomSpace />
    </>
  );
});

const ItemInput = memo(
  ({
    type,
    params,
    setParams,
  }: {
    type: IInputFilterType;
    params: any;
    setParams: any;
  }) => {
    const theme = useTheme();
    const title = useMemo(() => {
      if (type == 'receiver') return ['recipient'];
      if (type == 'doctype') return ['document_type'];
      if (type.indexOf('date') !== -1) return ['from_date', 'to_date'];
      return [type];
    }, [type]);

    const onChangeParams = useCallback(
      (key: string, val: any) => {
        if (key.indexOf('date') !== -1) {
          setParams((prev: any) => ({
            ...prev,
            [key]: dateToNum(val),
          }));
          return;
        }

        setParams((prev: any) => ({
          ...prev,
          [key]: val,
        }));

        if (key == 'collection') {
          setParams((prev: any) => ({
            ...prev,
            folder: '',
          }));
        }
      },
      [setParams],
    );

    /** --------------------------------------------------
     * Date input
     */
    if (type.indexOf('date') !== -1) {
      return (
        <>
          {/*<SDateTitle>{translate('qa.' + type)}</SDateTitle>*/}
          <SRow>
            <MyDateTimeInput
              containerStyle={{flex: 1}}
              mode={'date'}
              title={translate('qa.' + title[0])}
              value={dateFormat(params[type + '_from'], 'DD-MM-YYYY')}
              keyName={type + '_from'}
              onValueChange={onChangeParams}
              showClearButton={true}
            />
            <MyDateTimeInput
              containerStyle={{flex: 1, marginLeft: 16}}
              mode={'date'}
              title={translate('qa.' + title[1])}
              value={dateFormat(params[type + '_to'], 'DD-MM-YYYY')}
              keyName={type + '_to'}
              onValueChange={onChangeParams}
              showClearButton={true}
            />
          </SRow>
        </>
      );
    }

    // /** ---------------------------------------------
    //  *  Check box
    //  */
    // if (type.indexOf('answered') !== -1) {
    //   return (
    //     <DynamicCheckBox
    //       value={params[type]}
    //       multiple
    //       onPress={() => onChangeParams(type, !params[type])}
    //       styleCheckbox={{
    //         borderWidth: 2,
    //         borderColor: params[type] ? Colors.blue1 : theme.grey2,
    //         backgroundColor: params[type] ? Colors.blue1 : theme.white,
    //         marginLeft: 8,
    //         borderRadius: 1,
    //         width: 17,
    //         height: 17,
    //       }}
    //       styleLabel={{
    //         fontSize: 16,
    //         lineHeight: 20,
    //         fontFamily: Fonts.Regular,
    //         color: theme.grey1,
    //       }}
    //       label={translate('qa.' + type)}
    //     />
    //   );
    // }

    /** ---------------------------------------------
     *  User Input
     */
    if (type == 'user') {
      return (
        <UIInput.UserPicker
          title={'Updates by'}
          value={params[type] || []}
          keyName={type}
          onChangeValue={onChangeParams}
          // valueType={'userId'}
          // showClearButton={true}
          multiple
          type={'user'}
        />
      );
    }

    if (type == 'created_by') {
      return (
        // <MyUserPickerInput
        //   title={translate('qa.' + title[0])}
        //   value={params[type] || []}
        //   keyName={type}
        //   onValueChange={onChangeParams}
        //   valueType={'userId'}
        //   showClearButton={true}
        //   multiple
        // />
        <UIInput.UserPicker
          title={translate('qa.' + title[0])}
          value={params[type] || []}
          keyName={type}
          onChangeValue={onChangeParams}
          // valueType={'userId'}
          // showClearButton={true}
          multiple
          type={'user'}
        />
      );
    }

    /** --------------------------------------------------
     * Selector Input
     */
    const channelsOption = useChannelsByQuery('all').map((id:string) => ({
      label: getChannels(id)?.name || 'Undefined channel',
      value: id,
    }));
    if (type == 'channel') {
      return (
        <MySelectorInput
          title={'Channel'}
          value={params[type]}
          keyName={type}
          onValueChange={onChangeParams}
          options={[
            {
              label: 'All channels',
              value: '',
            },
            ...channelsOption,
          ]}
        />
      );
    }
    return (
      <MySelectorInput
        multiple
        title={translate('qa.' + title[0])}
        value={params[type]}
        keyName={type}
        onValueChange={onChangeParams}
        options={[]}
        showClearButton={true}
      />
    );
  },
);

const SItemContainer = styled.View`
  margin: 16px 16px 0 16px;
`;

const SRow = styled.View`
  flex-direction: row;
`;
const SDateTitle = styled.Text`
  font-size: 14px;
  line-height: 22px;
  font-family: ${Fonts.Bold};
  margin-bottom: 4px;
`;
const UIButtonWrapper = styled(FlexCenter)`
  margin: 16px 16px 0 16px;
`;
