import React, {RefObject, useCallback, useMemo} from 'react';
import {UIText} from '@true/ui';
import {memoForwardRef, styled} from '@true/core';
import {BottomSheetMenu} from '@/components/BottomSheets/index';
import {BottomSheetModal} from '@gorhom/bottom-sheet';
import {EOrderUpdate} from '@/store/updates/type';

interface Props {
  setOrder: (order: EOrderUpdate) => void;
  refreshHome: () => Promise<void>;
}

const OrderUpdateBottomSheet = memoForwardRef(
  ({setOrder, refreshHome}: Props, ref: RefObject<BottomSheetModal>) => {
    const options = useMemo(() => {
      return [
        {
          label: 'Recently updated',
          value: EOrderUpdate.UPDATE,
        },
        {
          label: 'Recently created',
          value: EOrderUpdate.CREATED,
        },
      ];
    }, []);

    const renderItem = useCallback(
      (item: any, index: number) => {
        return (
          <SButton
            onPress={() => {
              setOrder(item.value);
              refreshHome().then();
              ref.current?.dismiss();
            }}>
            <UIText.BodyMedium400>{item.label}</UIText.BodyMedium400>
          </SButton>
        );
      },
      [ref],
    );

    return (
      <BottomSheetMenu
        name={'order-update'}
        ref={ref}
        items={options}
        renderMenuItem={renderItem}
      />
    );
  },
);

const SButton = styled.TouchableOpacity`
  padding: 16px 0;
  margin: 0 16px;
  border-bottom-width: 1px;
  border-bottom-color: ${props => props.theme.neutral5};
`;
export default OrderUpdateBottomSheet;
