import {memoForwardRef} from '@true/core';
import {ScreenWrapper} from '@true/ui';
import {CustomHeader} from '@/components/CustomHeader';
import {useState} from 'react';
import {useNavigationParams} from '@true/core';
import {FilterComp} from '@/components/FilterComp';
import {IFilterParams} from '@/screens/SearchScreen/type';

export interface FilterParams {
  params: IFilterParams;
  setParams: (value: any) => void;
}

export const FilterScreen = memoForwardRef((props,ref) => {
  const {params, setParams} = useNavigationParams<FilterParams>();

  const [_params, _setParams] = useState(params);
  console.log('params', _params);

  return (
    <ScreenWrapper>
      <CustomHeader isBack title={'Filter'} isSearch={false} />
      <FilterComp
        items={['channel', 'user']}
        params={_params}
        setParams={_setParams}
        defaultParams={{}}
        onSubmit={setParams}
      />
    </ScreenWrapper>
  );
});
