import * as React from 'react';
import { ReactionEnum } from "@/utils/helper";
import {
  IC_EMOTION_ANGRY,
  IC_EMOTION_CARE,
  IC_EMOTION_HAHA,
  IC_EMOTION_LIKE, IC_EMOTION_LOVE,
  IC_EMOTION_SAD,
  IC_EMOTION_WOW
} from "@true/core";

const getReactionImageComponent = (reaction: ReactionEnum): React.MemoExoticComponent<any> => {
  if (reaction === ReactionEnum.LIKE) return IC_EMOTION_LIKE;
  if (reaction === ReactionEnum.ANGRY) return IC_EMOTION_ANGRY;
  if (reaction === ReactionEnum.SAD) return IC_EMOTION_SAD;
  if (reaction === ReactionEnum.WOW) return IC_EMOTION_WOW;
  if (reaction === ReactionEnum.HAHA) return IC_EMOTION_HAHA;
  if (reaction === ReactionEnum.CARE) return IC_EMOTION_CARE;
  return IC_EMOTION_LOVE
};

export const ReactionService = {
  getReactionImageComponent,
};

export default ReactionService;
