import React, {memo} from 'react';
import {styled} from '@true/core';
import {ActivityIndicatorProps} from 'react-native';

const Wrapper = styled.View`
  flex-direction: row;
  align-items: center;
  height: 44px;
  justify-content: center;
  padding-top: 8px;
`;

const Indicator = styled.ActivityIndicator.attrs(
  ({theme}): ActivityIndicatorProps => ({
    color: theme.name === 'light' ? theme.primaryColor : theme.grey1,
    size: 24,
  }),
)`
  margin-right: 12px;
`;

const Text = styled.Text`
  font-size: 14px;
  color: ${props => props.theme.grey1};
  line-height: 24px;
`;

const WrapperError = styled.View`
  align-items: center;
  justify-content: center;
  height: 60px;
`;

const RetryText = styled.Text`
  color: ${props =>
    props.theme.name === 'light'
      ? props.theme.primaryColor
      : props.theme.grey1};
  margin-top: 4px;
`;

const LoadingPreviousPost = memo(
  ({
    error,
    isLoading,
    onPressRetry,
  }: {
    error?: Error;
    isEmpty?: boolean;
    isLoading?: boolean;
    onPressRetry?: () => any;
  }) => {
    if (error) {
      return (
        <WrapperError>
          <Text>{error.message}</Text>
          <RetryText onPress={onPressRetry}>Retry</RetryText>
        </WrapperError>
      );
    }

    if (isLoading) {
      return (
        <Wrapper>
          <Indicator />
          <Text>Loading previous post</Text>
        </Wrapper>
      );
    }

    return null;
  },
);

export default LoadingPreviousPost;
