import {memo, useCallback} from 'react';
import {styled} from '@true/core';
import {Fonts} from '@true/core';
import {globalStyles} from '@/utils/globalStyle';
import {
  IC_FILLED_CHECK_CIRCLE,
  IC_OUTLINED_SCHEDULE,
  IC_OUTLINED_CHECK,
} from '@true/asset';
import {Buffer} from 'buffer';
import {IBase64} from '@/utils/helper';
import {useLetters} from '@/store/letters';
import {FlexCenter} from '@/utils/helper';
import {Avatar, HTMLRenderer} from '@true/ui';
import {Pressable, View} from 'react-native';
import {getUser} from '@true/core';
import moment from 'moment';
import { navigateToDetailPostScreen} from '@/utils';
import FastImage from 'react-native-fast-image';
interface LetterThumbnailProps {
  id: number;
}

export const LetterThumbnail = memo(function (props: LetterThumbnailProps) {
  const {id} = props;
  const data = useLetters(id);
  const user = getUser(data?.user_id.toString() || '0');
  const coverImage: IBase64 = JSON.parse(
    Buffer.from(
      data?.image ||
        'eyJ0eXBlIjoiIiwiY29sb3IiOiIiLCJvcGFjaXR5IjowLCJwZXJjZW50IjoxMDAsImltYWdlIjoiIiwidGV4dHMiOltdfQ',
      'base64',
    ).toString('utf-8'),
  );
  const openDetailScreen = useCallback(() => {
    navigateToDetailPostScreen({
      id: data?.update_id || 0,
      user_id: data?.user_id || 0,
      time: data?.since || 0,
      hid: data?.hid || '',
    });
  }, []);
  return (
    <SContainer>
      <SContentContainer>
        <SImageWrapper>
          <SCoverImage resizeMode={'cover'} source={{uri: coverImage.image}} />
        </SImageWrapper>
        <SInfoWrapper onPress={openDetailScreen}>
          <SRow>
            <View style={{flexDirection: 'row', flex: 1}}>
              <Avatar userKey={data?.user_id || '0'} size={40} />
              <SColumnSpaceBetween style={globalStyles.ml8}>
                <STextName>{user?.name}</STextName>
                <STextJob>{user?.title || 'No official title'}</STextJob>
              </SColumnSpaceBetween>
            </View>
            <FlexCenter>
              <SImageGreen source={IC_FILLED_CHECK_CIRCLE} />
              <STextGreen style={globalStyles.ml4}>Published</STextGreen>
            </FlexCenter>
          </SRow>
          <STitleWrapper>
            <STextTitle>{data?.name}</STextTitle>
          </STitleWrapper>
          <View
            style={{
              overflow: 'hidden',
              backgroundColor: 'white',
              marginBottom: 8,
              maxHeight: 32,
            }}>
            <HTMLRenderer
              htmlContent={data?.content || ''}
              defaultTextProps={{
                style: {
                  fontSize: 12,
                  lineHeight: 16,
                  color: '#242424',
                },
              }}
            />
          </View>

          <FlexCenter>
            <SImageGrey source={IC_OUTLINED_SCHEDULE} />
            <STextGrey style={globalStyles.ml8}>
              {moment(
                // @ts-ignore
                data?.last_update * 1000,
              ).format('dddd HH:mm DD/MM/YYYY')}
            </STextGrey>
            <STextDivider> | </STextDivider>
            {data?.seens && data?.seens.length > 0 ? (
              <FlexCenter>
                <STextGreen>{`Seen by ${data?.seens.length} `}</STextGreen>
                <STick source={IC_OUTLINED_CHECK} />
              </FlexCenter>
            ) : null}
          </FlexCenter>
          {/*<SRow style={{ marginLeft: 4 }}>*/}
          {/*  <AttendeeAvatarList size={20} ids={data?.seens} />*/}
          {/*  <STextDivider> | </STextDivider>*/}
          {/*  <STextGrey>{`${data?.seens.length} participants`}</STextGrey>*/}
          {/*</SRow>*/}
        </SInfoWrapper>
      </SContentContainer>
    </SContainer>
  );
});
const SContainer = styled.View`
  background-color: white;
`;
const SContentContainer = styled.TouchableOpacity`
  background-color: white;
  border-radius: 8px;
  border: 1px solid #ebebeb;
  margin: 16px 16px 0 16px;
`;
const SImageWrapper = styled.View`
  width: 100%;
`;
const SCoverImage = styled(FastImage)`
  width: 100%;
  height: 184px;
  max-height: 190px;
  border-top-right-radius: 8px;
  border-top-left-radius: 8px;
  background-color: #2a91d6;
`;

const SInfoWrapper = styled(Pressable)`
  padding: 8px 12px;
`;

const STitleWrapper = styled.View`
  flex-wrap: wrap;
  flex-direction: row;
  margin: 8px 0;
`;

const STextTitle = styled.Text`
  font-family: ${Fonts.Regular};

  font-weight: 700;
  font-size: 16px;
  color: #242424;
  line-height: 24px;
`;
const STextGrey = styled.Text`
  font-family: ${Fonts.Regular};

  font-weight: 400;
  font-size: 12px;
  line-height: 16px;
  color: #666666;
`;
const SColumnSpaceBetween = styled.View`
  justify-content: space-between;
`;
const SImageGrey = styled.Image`
  width: 16px;
  height: 16px;
  tint-color: #666666;
`;
const SImageGreen = styled(SImageGrey)`
  tint-color: #05944f;
`;
const SRow = styled.View`
  flex-direction: row;
  justify-content: space-between;
  align-items: flex-start;
`;
const STextDivider = styled.Text`
  color: #ebebeb;
`;
const STextName = styled.Text`
  font-family: ${Fonts.Regular};

  font-weight: 700;
  font-size: 14px;
  line-height: 20px;
  color: #242424;
`;
const STextJob = styled.Text`
  font-family: ${Fonts.Regular};

  font-weight: 400;
  font-size: 12px;
  line-height: 16px;
  color: #242424;
`;
const STextGreen = styled(STextJob)`
  color: #05944f;
`;
const STick = styled.Image`
  width: 12px;
  height: 12px;
  tint-color: #05944f;
`;
