import { memo, useCallback, useEffect } from "react";
import { goBack, styled, useAutoToastError, useBoolean } from "@true/core";
import { CustomHeader } from "@/components/CustomHeader";
import { uploadImage } from "@/utils/helper";
import { IEditCompany, initEditCompany, requestEditCompanyInfo } from "@/store/settings/function";
import { useSetting } from "@/store/settings";
import { DiscardDialog } from "@/components/DiscardDialog";
import { FormScroll, MyKeyboardAvoidingView, ScreenWrapper, UIInput } from "@true/ui";
import { AnimatedBottomSpace } from "@/components/AnimatedBottomSpace";
import { ImageDTO } from "@/components/CameraRollPicker/useGallery";
import { useAsyncFn } from "@/hooks/useAsyncFn";
import { runOnUI, useSharedValue } from "react-native-reanimated";

const BoardLetterSetting = memo(function BoardLetterSetting() {
  const setting = useSetting();
  const sysInfo = setting?.sysinfo;
  const [visibleDialog, showDialog, hideDialog] = useBoolean();

  const params = useSharedValue<IEditCompany>(initEditCompany);

  useEffect(() => {
    let _params = {
      ...initEditCompany
    };
    if (sysInfo) {
      _params = {
        title: sysInfo.title,
        logo: sysInfo.logo,
        image: sysInfo.image,
        address: sysInfo.address,
        vision: sysInfo.vision,
        founded: sysInfo.founded,
        mv_sticky: sysInfo.config.mv_sticky === 0 ? "" : "on"
      };
    }
    params.value = _params;
  }, [sysInfo, params]);

  const onGoBack = useCallback(() => {
    goBack();
  }, []);

  const [{ loading, error: createError }, onPressSubmit] =
    useAsyncFn(async () => {
      await requestEditCompanyInfo(params.value);
      goBack();
    }, []);
  useAutoToastError(createError);

  const onSelectBackground = useCallback(async (image: ImageDTO) => {
    const imageUrl = await uploadImage(image);
    onChangeValue("image", imageUrl);
  }, []);

  const onSelectLogo = useCallback(async (image: ImageDTO) => {
    const imageUrl = await uploadImage(image);
    onChangeValue("logo", imageUrl);
  }, []);

  const onChangeValue = useCallback(
    (key: string, value: any) => {
      runOnUI((_key: string, _value) => {
        "worklet";

        params.value = {
          ...params.value,
          [_key]: _value
        };
      })(key, value);
    },
    [params]
  );

  return (
    <ScreenWrapper>
      <MyKeyboardAvoidingView>
        <CustomHeader
          title={"CEO letters"}
          isSearch={false}
          isBack
          onCreate={onPressSubmit}
          okText={"Save"}
        />
        <FormScroll>
          <SWrapperInput>
            <UIInput.Selector
              title={"Permission to create letters"}
              keyName={"title"}
              params={params}
              options={[]}
              placeholder={"App admins and owners"}
            />
          </SWrapperInput>
          <SWrapperInput>
            <UIInput.Selector
              title={"Permission to create company channels"}
              keyName={"title"}
              params={params}
              options={[]}
              placeholder={"Everyone"}
            />
          </SWrapperInput>
          <SWrapperInput>
            <UIInput.Selector
              title={"Title displayed as"}
              keyName={"title"}
              params={params}
              options={[]}
              placeholder={"CEO letters"}
            />
          </SWrapperInput>
          {/*<SWrapperInput>*/}
          {/*  <MyCheckBox*/}
          {/*    defaultValue={watch('mv_sticky')}*/}
          {/*    control={control}*/}
          {/*    keyName={'mv_sticky'}*/}
          {/*    title={'Make missions and visions highlighted on the homepage'}*/}
          {/*    value={watch('mv_sticky')}*/}
          {/*  />*/}
          {/*</SWrapperInput>*/}
          <AnimatedBottomSpace />
        </FormScroll>
      </MyKeyboardAvoidingView>
      <DiscardDialog
        title={"Discard change"}
        content={
          "You have made changes to this item that have not been saved. If you leave this page, your changes will be lost."
        }
        visible={visibleDialog}
        onClose={hideDialog}
      />
    </ScreenWrapper>
  );
});

export default BoardLetterSetting;

const SWrapperInput = styled.View`
  margin: 8px 16px 0 16px;
`;
