export const wait = (timeout: number) => {
  return new Promise(resolve => setTimeout(resolve, timeout));
};

export const uploadImage = async (image: ImageDTO) => {
  const _file = {
    type: 'image',
    name: image.fileName,
    uri: image.url,
  };
  const {data} = await Fetch.postWithToken<{
    image: string;
  }>(`@${Core.systemPath}/api/ajax/api/file/upload?method=file`, {
    file: _file,
  });
  console.log('data', data);
  return data.image;
};
