import {
  PreviewCommentList,
  ScreenWrapper,
  UIButton,
  UIFlashList,
  UIText,
} from '@true/ui';
import React, {createRef, memo, useCallback, useRef, useState} from 'react';
import {goBack, styled, useAsyncFn, useMe} from '@true/core';
import {PostThumbnail} from '@/components/PostThumbnail';
import {
  requestLoadMoreUpdates,
  requestUpdatePosts,
} from '@/store/updates/function';
import {useUpdatesByQuery} from '@/store/updates';
import {FlashList} from '@shopify/flash-list';
import {PinnedUpdates} from '@/screens/HomeScreen/PinnedUpdates';
import {UpdateSkeleton} from '@/components/SkeletonPlaceholder/UpdateSkeleton';
import {
  navigateToProfileScreen,
  navigateToSearchScreen,
  openSelectTypePostScreen,
} from '@/utils';
import {useTokenUpdate} from '@/zustand/TokenUpdate';
import {FlexCenter, sleep} from '@/utils/helper';
import LoadingPreviousPost from '@/components/LoadingPreviousPost';
import {HomeHeader} from '@/screens/HomeScreen/HomeHeader';
import {
  useAnimatedScrollHandler,
  useSharedValue,
} from 'react-native-reanimated';
import {IC_OUTLINED_EXPAND_MORE} from '@true/asset';
import {EOrderUpdate} from '@/store/updates/type';
import {BottomSheetModal} from '@gorhom/bottom-sheet';
import OrderUpdateBottomSheet from '@/components/BottomSheets/OrderUpdate';
import {TouchableOpacity} from 'react-native';

export const homeScreenRef = createRef<FlashList<any>>();

const HomeScreen = memo(function () {
  const listUpdates = useUpdatesByQuery('all');
  const listPinnedUpdates = useUpdatesByQuery('pinned');
  const ptoken = useTokenUpdate();
  const [order, setOrder] = useState(EOrderUpdate.UPDATE);
  const orderUpdateRef = useRef<BottomSheetModal>(null);
  const commentListRef = useRef<PreviewCommentList>(null);
  const offsetY = useSharedValue(0);
  const me = useMe();

  const [{loading}, refreshHome] = useAsyncFn(async () => {
    requestUpdatePosts({order}).then();
  }, []);

  const renderItem = useCallback(({item}: any) => {
    return <PostThumbnail isInFeed={true} id={item} ref={commentListRef} />;
  }, []);
  const renderHeader = useCallback(() => {
    return (
      <SHeaderFlatList>
        {listPinnedUpdates && listPinnedUpdates.length > 0 ? (
          <PinnedUpdates listIds={listPinnedUpdates} />
        ) : null}
        <SContentContainer>
          <TouchableOpacity onPress={onChangeOrderUpdate}>
            <FlexCenter>
              <UIText.HeaderMedium>
                {order === EOrderUpdate.UPDATE
                  ? 'Recently updated'
                  : 'Recently created'}
              </UIText.HeaderMedium>
              <UIButton source={IC_OUTLINED_EXPAND_MORE} />
            </FlexCenter>
          </TouchableOpacity>
        </SContentContainer>
      </SHeaderFlatList>
    );
  }, [listPinnedUpdates.length, order]);

  const [{loading: loadingPrev, error: errorPrev}, loadMoreMessage] =
    useAsyncFn(async () => {
      await requestLoadMoreUpdates({ptoken});
      await sleep(0.5);
      return [];
    }, [listUpdates.length, ptoken]);

  const onEndReached = useCallback(() => {
    if (loadingPrev) return;
    // if (errorPrev) return;
    // if (!messages.length) return;
    loadMoreMessage().then();
  }, [loadingPrev, errorPrev, listUpdates]);

  const renderFooter = useCallback(() => {
    return (
      <LoadingPreviousPost
        isEmpty={listUpdates.length === 0}
        isLoading={loadingPrev}
        error={errorPrev}
        onPressRetry={loadMoreMessage}
      />
    );
  }, [loadingPrev, errorPrev, listUpdates.length]);

  const onLoadData = useCallback(
    async (isRefreshing: boolean, isLoadMore: boolean) => {
      if (isLoadMore) {
        await loadMoreMessage();
      }
    },
    [loadMoreMessage],
  );

  const onScroll = useAnimatedScrollHandler(event => {
    offsetY.value = event.contentOffset.y;
  });
  const onChangeOrderUpdate = useCallback(() => {
    orderUpdateRef.current?.present();
  }, []);

  return (
    <ScreenWrapper isGrey={true}>
      <HomeHeader
        isSearch
        onSearch={() => {
          navigateToSearchScreen({
            isFilter: false,
          });
        }}
        onPress={openSelectTypePostScreen}
        animatedOffsetY={offsetY}
        onLeftPress={goBack}
      />
      <UIFlashList
        // ref={homeScreenRef}
        data={listUpdates}
        extraData={listUpdates}
        renderItem={renderItem}
        refreshing={loading}
        refreshable
        onRefresh={refreshHome}
        onEndReachedThreshold={1}
        ListHeaderComponent={renderHeader}
        ListEmptyComponent={<UpdateSkeleton />}
        // nestedScrollEnabled={true}
        scrollEventThrottle={16}
        estimatedItemSize={590}
        removeClippedSubviews={true}
        // onEndReached={onEndReached}
        ListFooterComponent={renderFooter}
        onLoadData={onLoadData}
        loadingMore={loadingPrev}
        loadMorable
        onScroll={onScroll}
        keyExtractor={item => item.toString()}
        drawDistance={300}
        viewabilityConfig={{
          waitForInteraction: true,
          itemVisiblePercentThreshold: 50,
          minimumViewTime: 1000,
        }}
      />
      <OrderUpdateBottomSheet
        ref={orderUpdateRef}
        setOrder={setOrder}
        refreshHome={refreshHome}
      />
    </ScreenWrapper>
  );
});
export default HomeScreen;

const SHeaderFlatList = styled.View``;

const SContentContainer = styled.View`
  flex-direction: row;
  align-items: center;
  padding: 12px 16px;
`;
