import React, {memo, useCallback} from 'react';
import {styled} from '@true/core';
import {CustomHeader} from '@/components/CustomHeader';
import {ScreenWrapper, ScrollableTabView} from '@true/ui';
import {getChannelsByQuery, useChannelsByQuery} from '@/store/channels';
import {requestAllChannels} from '@/store/channels/function';
import {SettingChannelThumbnail} from '@/screens/SettingScreen/SettingChannel/SettingChannelThumbnail';
import {navigateToCreateChannelScreen} from '@/utils';
import { ETypeChannel } from "@/store/channels/type";

export const SettingChannel = memo(function SettingChannel() {
  const renderItem = useCallback(
    (item: any, index?: number, isLast?: boolean) => {
      return <SettingChannelThumbnail id={item} />;
    },
    [],
  );
  return (
    <ScreenWrapper isGrey={true}>
      <CustomHeader
        title={'Channels'}
        isSearch={true}
        onCreate={navigateToCreateChannelScreen}
        isBack
      />
      <SContainer>
        <ScrollableTabView.Provider
          params={{}}
          renderItem={renderItem}
          getItemByQuery={getChannelsByQuery}
          useItemByQuery={useChannelsByQuery}
          tabs={[
            {
              title: 'Team/Department',
              content: p => (
                <ScrollableTabView.List
                  {...p}
                  itemHeight={210}
                  requestFn={requestAllChannels}
                  tab={'team'}
                />
              ),
            },
            {
              title: 'Company wide',
              content: p => (
                <ScrollableTabView.List
                  {...p}
                  itemHeight={210}
                  requestFn={requestAllChannels}
                  tab={ETypeChannel.COMPANY}
                />
              ),
            },
          ]}>
          <ScrollableTabView.TabBar />
          <ScrollableTabView.Content />
        </ScrollableTabView.Provider>
      </SContainer>
    </ScreenWrapper>
  );
});
const SContainer = styled.View`
  flex: 1;
  background-color: white;
`;
