import {memo, useCallback, useEffect} from 'react';
import {ActivityIndicator} from 'react-native';
import {styled} from '@true/core';
import {FlashList} from '@shopify/flash-list';
import {useLettersByQuery} from '@/store/letters';
import {LetterThumbnail} from '@/screens/BoardLetterScreen/LetterThumbnail';
import {useAsyncFn} from '@true/core';
import {requestLetters} from '@/store/letters/function';
import {CustomHeader} from '@/components/CustomHeader';
import {ScreenWrapper} from '@true/ui';
import {navigateToCreateLetterScreen} from '@/utils';

export const BoardLetterScreen = memo(function () {
  const listLetter = useLettersByQuery('all');
  const renderItem = useCallback(({item, index}: any) => {
    return <LetterThumbnail id={item} />;
  }, []);

  const [{loading}, refreshLetter] = useAsyncFn(async () => {
    await requestLetters();
    return;
  }, []);

  useEffect(() => {
    requestLetters().then();
  }, []);

  return (
    <ScreenWrapper>
      <CustomHeader
        title={'CEO Letters'}
        isSearch={false}
        onCreate={() => {
          navigateToCreateLetterScreen({id: -1});
        }}
        isBack={true}
      />
      <SFlashList
        data={listLetter}
        renderItem={renderItem}
        keyExtractor={keyExtractor}
        refreshing={loading}
        onRefresh={refreshLetter}
        onEndReachedThreshold={1}
        removeClippedSubviews={true}
        // ListHeaderComponent={renderHeader}
        ListEmptyComponent={<ActivityIndicator />}
      />
    </ScreenWrapper>
  );
});

const SFlashList = styled(FlashList)`
  flex: 1;
`;
const keyExtractor = (item: any) => item;
