import React, {memo, useState} from 'react';
import {FlexCenter} from '@/utils/helper';
import {styled} from '@true/core';
import {UIText} from '@true/ui';
import {useStyledUpdate} from './provider';
import Animated, {
  clamp,
  runOnJS,
  useAnimatedGestureHandler,
  useAnimatedReaction,
  useAnimatedStyle,
  useSharedValue,
} from 'react-native-reanimated';
import {PanGestureHandler} from 'react-native-gesture-handler';
import {IC_OUTLINED_OPACITY} from '@true/asset';
import {Dimensions} from 'react-native';

const {width: DWidth} = Dimensions.get('screen');

const PercentText = memo(() => {
  const {selectedOpacity} = useStyledUpdate();
  const [percent, setPercent] = useState(selectedOpacity.value);

  useAnimatedReaction(
    () => selectedOpacity.value,
    cur => {
      runOnJS(setPercent)(cur * 100);
    },
    [setPercent],
  );

  return <SPercent>{`${percent.toFixed()} %`}</SPercent>;
});

export const OpacityPicker = memo(() => {
  const {selectedOpacity, selectedColor} = useStyledUpdate();
  const translateX = useSharedValue(
    (DWidth - 16 * 2 - 32) * selectedOpacity.value,
  );

  const gestureHandler = useAnimatedGestureHandler({
    onStart: (ev, ctx: any) => {
      ctx.offsetX = translateX.value;
    },
    onActive: (ev, ctx: any) => {
      translateX.value = clamp(
        ctx.offsetX + ev.translationX,
        0,
        DWidth - 16 * 2 - 32,
      );
      selectedOpacity.value =
        translateX.value > 0
          ? Math.round(
              (translateX.value / (DWidth - 16 * 2 - 32) + Number.EPSILON) *
                100,
            ) / 100
          : 0;
    },
  });

  const animatedStyle = useAnimatedStyle(() => ({
    transform: [{translateX: translateX.value}],
  }));

  const animateProgressStyle = useAnimatedStyle(() => ({
    width: translateX.value,
    backgroundColor: selectedColor.value,
  }));

  const animateSubProgressStyle = useAnimatedStyle(() => ({
    backgroundColor: selectedColor.value,
  }));

  return (
    <>
      <FlexCenter>
        <SText>Background opacity</SText>
        <PercentText />
      </FlexCenter>
      <SContainer>
        <SProgress style={animateProgressStyle}>
          <Animated.View
            style={{
              height: '100%',
              width: '100%',
              backgroundColor: 'white',
              opacity: 0.3,
            }}
          />
        </SProgress>
        <PanGestureHandler onGestureEvent={gestureHandler}>
          <SOpacityView style={animatedStyle}>
            <SOpacityIcon source={IC_OUTLINED_OPACITY} />
          </SOpacityView>
        </PanGestureHandler>
        <SSubBg style={animateSubProgressStyle}>
          <Animated.View
            style={{
              height: '100%',
              width: '100%',
              backgroundColor: 'white',
              opacity: 0.7,
            }}
          />
        </SSubBg>
      </SContainer>
    </>
  );
});

const SProgress = styled(Animated.View)`
  height: 6px;
  position: absolute;
  border-radius: 40px;
`;

const SSubBg = styled(Animated.View)`
  height: 6px;
  position: absolute;
  border-radius: 40px;
  right: 0;
  left: 0;
  z-index: -1;
`;

const SContainer = styled.View`
  height: 56px;
  width: 100%;
  justify-content: center;
`;

const SText = styled(UIText.BodyMedium400)`
  color: ${p => p.theme.neutral1};
  flex: 1;
`;

const SPercent = styled(UIText.BodyMedium500)`
  color: ${p => p.theme.neutral1};
`;

const SOpacityView = styled(Animated.View)`
  width: 32px;
  height: 32px;
  border-radius: 100px;
  border: 1px solid ${p => p.theme.neutral4};
  background-color: ${p => p.theme.backgroundColor};
  align-items: center;
  justify-content: center;
`;

const SOpacityIcon = styled.Image`
  width: 24px;
  height: 24px;
`;
