import {memo, useCallback, useMemo} from 'react';
import {useUpdates} from '@/store/updates';
import {Fonts, stripSpecialCharacters, styled, useTheme} from '@true/core';
import {HTMLRenderer, UIButton, UIText, UITextStyle} from '@true/ui';
import {navigateToDetailPostScreen} from '@/utils';
import SeeMore from '@/components/Text/SeeMore';
import {View} from 'react-native';

interface Props {
  id: number;
  isInFeed?: boolean;
}

export const PollContent = memo(({id, isInFeed}: Props) => {
  const data = useUpdates(id);
  const theme = useTheme();

  const dataPoll = useMemo(() => data?.attachment, [data?.attachment]);

  const onNavigateToDetail = useCallback(() => {
    navigateToDetailPostScreen({
      id: id,
      user_id: data?.user_id || 0,
      time: data?.since || 0,
      hid: data?.hid || '',
    });
  }, [data]);

  return (
    <UIButton onPress={onNavigateToDetail}>
      <STextHeading>{stripSpecialCharacters(dataPoll.name)}</STextHeading>
      {dataPoll.content ? (
        <View>
          <SDivider />
          {isInFeed ? (
            <SSeeMore id={'poll' + id.toString()} numberOfLines={4}>
              {dataPoll.content || ''}
            </SSeeMore>
          ) : (
            <HTMLRenderer
              htmlContent={dataPoll.content || ''}
              defaultTextProps={{
                style: {
                  ...UITextStyle.body_medium_400,
                  color: theme.neutral1,
                },
              }}
            />
          )}
        </View>
      ) : null}
    </UIButton>
  );
});

const STextHeading = styled(UIText.HeaderMedium)`
  color: ${p => p.theme.neutral1};
`;

const SSeeMore = styled(SeeMore).attrs(p => ({
  linkStyle: {
    ...UITextStyle.body_medium_500,
    color: p.theme.primaryColor,
  },
}))`
  color: ${p => p.theme.neutral1};
  font-size: 14px;
  line-height: 20px;
  font-family: ${Fonts.Regular};
`;

const SDivider = styled.View`
  width: 100%;
  height: 1px;
  margin: 8px 0;
  background-color: ${p => p.theme.neutral5};
`;
