import {Fonts} from '@true/core';
import {memo, useCallback, useState} from 'react';
import {styled} from '@true/core';
import {IC_OUTLINED_IMAGE} from '@true/asset';
import {openCameraRollScreen} from '@/utils';
import {ImageDTO} from '@/components/CameraRollPicker/useGallery';

interface MyInputImageProps {
  onSelectedImage: (image: ImageDTO) => void;
  image: string | undefined;
}

export const MyInputImage = memo(function (props: MyInputImageProps) {
  const {onSelectedImage, image} = props;
  const [imageUri, setImageUri] = useState('');

  const onSelect = useCallback((image: ImageDTO) => {
    setImageUri(image.url);
    onSelectedImage(image);
  }, []);
  const onPress = useCallback(() => {
    openCameraRollScreen({
      onSubmit: onSelect,
    });
  }, []);

  return (
    <SContainer>
      <SBackgroundWrapper
        resizeMode={'cover'}
        source={{uri: imageUri ? imageUri : image}}>
        <SButtonAction onPress={onPress}>
          <SIconPhoto>
            <SImageGallery source={IC_OUTLINED_IMAGE} />
          </SIconPhoto>
          <STextAction>
            {imageUri || image ? 'Edit photo' : 'Add photo'}
          </STextAction>
        </SButtonAction>
      </SBackgroundWrapper>
    </SContainer>
  );
});
const SContainer = styled.TouchableOpacity`
  align-items: flex-start;
  justify-content: center;
  border-radius: 8px;
  border: 1px solid #ebebeb;
`;

const SBackgroundWrapper = styled.ImageBackground`
  background-color: #c7c7c7;
  width: 100%;
  min-height: 120px;
`;
const SButtonAction = styled.TouchableOpacity`
  position: absolute;
  bottom: 8px;
  right: 16px;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  padding: 4px 16px;
  background-color: rgba(0, 0, 0, 0.65);
  border-radius: 40px;
`;
const SIconPhoto = styled.View`
  width: 16px;
  height: 16px;
  align-items: center;
  justify-content: center;
  margin-right: 4px;
`;
const SImageGallery = styled.Image`
  width: 12px;
  height: 12px;
`;
const STextAction = styled.Text`
  font-family: ${Fonts.Regular};
  
  font-weight: 500;
  font-size: 14px;
  line-height: 24px;
  color: #ffffff;
`;
