import {memo} from 'react';
import {IC_OUTLINED_CLOSE} from '@true/asset';
import {UIButton, UIText} from '@true/ui';
import {styled} from '@true/core';

interface Props {
  title: string;
  onPress: () => void;
}

const BottomSheetHeader = memo(({title, onPress}: Props) => {
  return (
    <SHeaderWrapper>
      <SHeaderContainer>
        <STitleHeader>{title}</STitleHeader>
        <SCloseButton onPress={onPress} />
      </SHeaderContainer>
      <SMenuDivider />
    </SHeaderWrapper>
  );
});

const SHeaderWrapper = styled.View``;

const SHeaderContainer = styled.View`
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  padding: 8px 16px;
  height: 64px;
`;

const SMenuDivider = styled.View`
  height: 1px;
  background-color: ${p => p.theme.neutral5};
`;

const STitleHeader = styled(UIText.HeaderLarge)`
  color: ${p => p.theme.neutral1};
`;

const SCloseButton = styled(UIButton).attrs(p => ({
  source: IC_OUTLINED_CLOSE,
  imageSize: 24,
  imageStyle: {
    tintColor: p.theme.neutral1,
  },
}))``;

export default BottomSheetHeader;
