import {styled} from '@true/core';
import Animated, {
  useAnimatedStyle,
  useSharedValue,
} from 'react-native-reanimated';
import {LayoutChangeEvent, StyleSheet} from 'react-native';
import {HeaderPost} from '@/components/PostThumbnail/HeaderPost';
import React, {memo, useCallback, useMemo} from 'react';
import {useUpdates} from '@/store/updates';
import {navigateToDetailPostScreen, openDetailEventScreen} from '@/utils';
import {ETypeUpdate} from '@/store/updates/type';
import {ButtonHTMLExtend} from '@/components/ButtonHTMLExtend';
import {PostContent} from '@/components/PostThumbnail/PostContent';
import {useMounted} from '@/hooks/useMounted';
import {PinnedPostSkeleton} from '@/components/SkeletonPlaceholder/PinnedPostSkeleton';

export const MAX_HEIGHT_PINNED_POST = 240 - 26;
export const MAX_IMAGE_HEIGHT_PINNED_POST = 120;
export const MAX_IMAGE_HEIGHT_POST = 210;

interface PinnedPostThumbnailProps {
  id: number;
}

export const PinnedPostThumbnail = memo(function (
  props: PinnedPostThumbnailProps,
) {
  const {id} = props;
  const dataPost = useUpdates(id);
  const type = useMemo(() => dataPost?.truetype, [dataPost?.truetype]);
  const mounted = useMounted(300);
  const height = useSharedValue(0);
  const onLayout = useCallback((e: LayoutChangeEvent) => {
    height.value = e.nativeEvent?.layout?.height;
  }, []);
  const animatedContainerStyle = useAnimatedStyle(() => {
    return {
      maxHeight:
        height.value > MAX_HEIGHT_PINNED_POST
          ? MAX_HEIGHT_PINNED_POST
          : undefined,
      overflow: 'hidden',
    };
  });
  const onNavigateToDetail = useCallback(() => {
    if (
      type == ETypeUpdate.ANNOUNCEMENT ||
      type === ETypeUpdate.UPDATE ||
      type === ETypeUpdate.IDEA ||
      type === ETypeUpdate.TOPIC ||
      type === ETypeUpdate.POLL ||
      type === ETypeUpdate.WELCOME ||
      type === ETypeUpdate.COMPANY_LETTER ||
      type === ETypeUpdate.CORE_VALUE
    ) {
      navigateToDetailPostScreen({
        id: id || 0,
        user_id: dataPost?.user_id || 0,
        time: dataPost?.since || 0,
        hid: dataPost?.hid || '',
      });
      return;
    }
    if (type === ETypeUpdate.EVENT) {
      openDetailEventScreen({
        id: dataPost?.attachment.id,
      });
      return;
    }
    return;
  }, [type]);

  return (
    <>
      <SContainer>
        <SHeaderPost
          type={dataPost?.truetype}
          user_id={dataPost?.user_id}
          channel_id={dataPost?.channel_id}
          time={dataPost?.since}
          isInFeed={false}
          id={id}
          hid={dataPost?.hid || ''}
          avatarSize={32}
          isPin
        />
        <Animated.View style={animatedContainerStyle}>
          <Animated.View onLayout={onLayout}>
            <PostContent isInFeed={true} id={id} isPin />
          </Animated.View>
        </Animated.View>
        <ButtonHTMLExtend
          onPress={onNavigateToDetail}
          height_content={height}
          maxHeight={MAX_HEIGHT_PINNED_POST}
          isPinned
        />
        <Animated.View
          style={{
            ...StyleSheet.absoluteFillObject,
            zIndex: mounted ? -1 : 1,
            opacity: mounted ? 0 : 1,
          }}>
          <PinnedPostSkeleton />
        </Animated.View>
      </SContainer>
    </>
  );
});

const SContainer = styled.View`
  padding: 12px 0 8px 0;
  margin-left: 12px;
  width: 266px;
  height: 308px;
  border-radius: 8px;
  border: 1px solid ${p => p.theme.neutral6};
  box-shadow: 0 4px 16px rgba(223, 227, 236, 0.16);
  background-color: ${p => p.theme.backgroundColor};
  overflow: hidden;
`;

const SHeaderPost = styled(HeaderPost)`
  padding: 0 12px;
`;
