import {ComponentType, memo, useMemo, useState} from 'react';
import {styled, useMe} from '@true/core';
import {Fonts} from '@true/core';
import {View} from 'react-native';
import {globalStyles} from '@/utils/globalStyle';
import {FlexCenter} from '@/utils/helper';
import {IReaction} from '@/store/updates/type';
import FastImage from 'react-native-fast-image';
import ReactionService from '@/services/ReactionService';
import {ReactionEnum} from '@/utils/helper';

interface ReactionListProps {
  withNumber: boolean;
  likes?: number;
  reactions?: IReaction[];
}

export const ReactionList = memo(function (props: ReactionListProps) {
  const {withNumber, likes, reactions} = props;
  const [meLiked, setMeLiked] = useState(false);
  const me = useMe();
  const listTypeReactions = useMemo(
    () =>
      reactions?.map(item => {
        if (me?.id == item.user_id) setMeLiked(true);
        return item.reaction;
      }),
    [reactions],
  );
  const _listReactions = [...new Set(listTypeReactions)];

  const title = useMemo(() => {
    if (meLiked && likes && likes > 1) {
      return `You and ${likes - 1} others`;
    }

    if (meLiked && (!likes || (likes && likes <= 1))) {
      return 'You';
    }

    return (likes || 0).toString();
  }, [meLiked, likes]);

  return (
    <SContainer>
      <SContentContainer>
        {withNumber && (
          <View style={globalStyles.mr8}>
            <SNumberReactions>{likes}</SNumberReactions>
          </View>
        )}
        <FlexCenter style={globalStyles.ml4}>
          {_listReactions
            ?.slice(0, 2)
            .map((item: ReactionEnum, index: number) => {
              return (
                <SIcon key={index}>
                  <StyledReactionImage
                    // @ts-ignore
                    source={ReactionService.getReactionImageComponent(item)}
                  />
                </SIcon>
              );
            })}
        </FlexCenter>
        {!withNumber && (
          <View style={globalStyles.ml4}>
            <SText>{title}</SText>
          </View>
        )}
      </SContentContainer>
    </SContainer>
  );
});
const SContainer = styled.View``;
const SContentContainer = styled.View`
  background-color: white;
  flex-direction: row;
  align-items: center;
`;
const SIcon = styled.View`
  align-items: center;
  justify-content: center;
  border: 1px solid #ffffff;
  border-radius: 10px;
  margin-left: -4px;
`;

const SText = styled.Text`
  font-family: ${Fonts.Regular};
  
  font-size: 14px;
  line-height: 20px;
  color: ${p => p.theme.neutral2};
`;
const SNumberReactions = styled.Text`
  font-family: ${Fonts.Regular};
  
  font-weight: 400;
  font-size: 14px;
  line-height: 20px;
  color: #666666;
`;
const StyledReactionImage = styled(FastImage)`
  width: 18px;
  height: 18px;
` as ComponentType<{width: number; height: number; as: ComponentType<any>}>;
