import React, {memo, useCallback, useMemo} from 'react';
import {IAttachmentFile} from '@/components/AttachmentFile/type';
import {AttachmentFile} from '@/components/AttachmentFile';
import {styled} from '@true/core';
import {useSharedValue} from 'react-native-reanimated';
import Metrics from '@/assets/Metrics';
import {PostImage} from '@/components/PostThumbnail/PostImage';
import {AttachmentImage} from '@/components/AttachmentFile/AttachmentImage';
import {FlexCenter} from '@/utils/helper';
import {StyleSheet, View} from 'react-native';
import {UIText} from '@true/ui';
import {navigatePhotoViewScreen} from '@/utils';

interface Props {
  files?: IAttachmentFile[];
}

export const AttachmentFilePost = memo(({files}: Props) => {
  const imageWidth = useSharedValue(Metrics.screen.width / 2);
  const isOnlyImage = useMemo(() => {
    let check = true;
    if (files && files.length) {
      files.forEach(file => {
        if (file) {
          const fileExtension = file.name.split('.').pop()?.toLowerCase() || '';
          if (
            !['jpg', 'jpeg', 'png', 'gif', 'bmp', 'webp'].includes(
              fileExtension,
            )
          ) {
            check = false;
          }
        }
      });
    }

    return check;
  }, [files]);

  const onPressImage = useCallback(
    (index: number) => {
      navigatePhotoViewScreen({
        initialIndex: index || 0,
        images: (files || [])?.map(file => file.url),
      });
    },
    [files],
  );

  if (files && files.length) {
    if (isOnlyImage) {
      switch (files.length) {
        case 0:
          return null;
        case 1:
          return (
            <SWrapper>
              <AttachmentImage
                image={files[0].src}
                index={0}
                onPressImage={onPressImage}
              />
            </SWrapper>
          );

        case 2:
          return (
            <SWrapper>
              <AttachmentImage
                image={files[0].src}
                index={0}
                onPressImage={onPressImage}
              />
              <AttachmentImage
                image={files[1].src}
                index={1}
                onPressImage={onPressImage}
              />
            </SWrapper>
          );
        case 3:
          return (
            <SWrapper>
              <AttachmentImage
                image={files[0].src}
                index={0}
                onPressImage={onPressImage}
              />
              <FlexCenter>
                <AttachmentImage
                  image={files[1].src}
                  imageWidth={imageWidth}
                  index={1}
                  onPressImage={onPressImage}
                />
                <AttachmentImage
                  image={files[2].src}
                  imageWidth={imageWidth}
                  index={2}
                  onPressImage={onPressImage}
                />
              </FlexCenter>
            </SWrapper>
          );
        default:
          return (
            <SWrapper>
              <AttachmentImage
                image={files[0].src}
                index={0}
                onPressImage={onPressImage}
              />
              <FlexCenter>
                <AttachmentImage
                  image={files[1].src}
                  imageWidth={imageWidth}
                  index={1}
                  onPressImage={onPressImage}
                />
                <AttachmentImage
                  image={files[2].src}
                  imageWidth={imageWidth}
                  index={2}
                  onPressImage={onPressImage}
                />
                <SOverlayBg onPress={() => onPressImage(2)}>
                  <STitle>{`+${files.length - 3}`}</STitle>
                </SOverlayBg>
              </FlexCenter>
            </SWrapper>
          );
      }
    }

    return (
      <SSectionAttachment>
        {files.map((file: IAttachmentFile, index: number) => {
          return (
            <SItemAttachment>
              <AttachmentFile attachment={file} time={file.since} key={index} />
            </SItemAttachment>
          );
        })}
      </SSectionAttachment>
    );
  }

  return null;
});

const SSectionAttachment = styled.View`
  background-color: white;
  flex-direction: row;
  align-items: center;
  padding: 0 16px 0 16px;
  flex-wrap: wrap;
  justify-content: space-between;
`;
const SItemAttachment = styled.View`
  min-width: 40%;
`;

const SWrapper = styled.View`
  margin-top: 8px;
`;

const STitle = styled(UIText.BodyLarge500)`
  color: ${p => p.theme.white};
  margin-top: 4px;
`;

const SRole = styled(UIText.BodyLarge400)`
  color: ${p => p.theme.white};
`;

const SOverlayBg = styled.TouchableOpacity`
  position: absolute;
  top: 0;
  bottom: 0;
  left: 50%;
  right: 0;
  background-color: rgba(0, 0, 0, 0.2);
  align-items: center;
  justify-content: center;
`;
