import {memo, useCallback} from 'react';
import {styled} from '@true/core';
import {formatPlainText, navigateToDetailPostScreen} from '@/utils';
import {useUpdates} from '@/store/updates';
import {useSharedValue, useWorkletCallback} from 'react-native-reanimated';
import {LayoutChangeEvent, StyleSheet, View} from 'react-native';
import {HTMLRenderer, UIButton, UIText, UITextStyle} from '@true/ui';
import {PostImage} from '@/components/PostThumbnail/PostImage';
import {ScrollView} from 'react-native-gesture-handler';

interface StyledPostProps {
  isInFeed: boolean;
  id: number;
}

export const StyledPost = memo(function (props: StyledPostProps) {
  const {id, isInFeed} = props;
  const imageWidth = useSharedValue(0);
  const data = useUpdates(id);
  const onNavigateToDetail = useCallback(() => {
    navigateToDetailPostScreen({
      id: id,
      user_id: data?.user_id || 0,
      time: data?.since || 0,
      hid: data?.hid || '',
    });
  }, []);

  const onLayout = useCallback(
    ({
      nativeEvent: {
        layout: {width},
      },
    }: LayoutChangeEvent) => {
      imageWidth.value = width;
    },
    [],
  );

  return (
    <SWrapper>
      <SContentView onLayout={onLayout}>
        <ScrollView showsVerticalScrollIndicator={false}>
          <SContainer onPress={onNavigateToDetail}>
            <View
              style={{
                overflow: 'hidden',
                backgroundColor: 'transparent',
                justifyContent: 'center',
                alignItems: 'center',
                minHeight: 200,
              }}>
              <HTMLRenderer
                htmlContent={data?.content || ''}
                defaultTextProps={{
                  style: {
                    ...UITextStyle.header_x_large,
                    color: '#ffffff',
                    textAlign: 'center',
                  },
                }}
              />
            </View>
          </SContainer>
        </ScrollView>
      </SContentView>
      <SImageWrapper style={StyleSheet.absoluteFillObject}>
        <PostImage image={data?.image} imageWidth={imageWidth} />
      </SImageWrapper>
    </SWrapper>
  );
});
const SWrapper = styled.View``;

const SContainer = styled(UIButton)``;

const STextContent = styled(UIText.HeaderXLarge)`
  color: ${p => p.theme.white};
`;

const SContentView = styled.View`
  padding: 16px;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 216px;
  overflow: scroll;
`;

const SImageWrapper = styled.View`
  align-items: center;
  justify-content: center;
  z-index: -1;
`;
