import {create} from 'zustand';
import {shallow} from 'zustand/shallow';

const indexMapStore = create<any>(() => ({
  indexMap: {},
}));

export const useIndex = (id: number | string) => {
  return indexMapStore((state: any) => state.indexMap[id], shallow);
};
export const setIndexMap = (value: number, id: number) => {
  return indexMapStore.setState((prev: any) => ({
    indexMap: {
      ...prev.indexMap,
      [id]: value,
    },
  }));
};
