import * as React from 'react';
import {
  ImageStyle,
  Platform,
  StyleSheet,
  TextInput,
  TouchableOpacity,
  ViewStyle,
} from 'react-native';
import {BottomSheetTextInput} from '@gorhom/bottom-sheet';
import { Colors, Fonts, styled, useTheme } from "@true/core";
import { IC_OUTLINED_SEARCH, IC_OUTLINED_CLOSE, IC_FILLED_CANCEL } from "@true/asset";
import {flatStyles} from '@true/core';
import {useCallback, useEffect, useMemo, useRef, useState} from 'react';
import {useRefState} from '@true/core';
import _ from 'lodash';
import {interactManager, memoForwardRef} from '@true/core';
import Animated, { useAnimatedStyle, useSharedValue, useWorkletCallback } from "react-native-reanimated";

const GrayPlaceholder = styled(TextInput).attrs(props => ({
  placeholderTextColor: props.theme.grey2,
  selectionColor: '#007AFF',
}))`
  color: ${props => props.theme.grey1};
`;

const ModalGrayPlaceholder = styled(BottomSheetTextInput).attrs(props => ({
  placeholderTextColor: props.theme.grey2,
  selectionColor: '#007AFF',
}))`
  color: ${props => props.theme.grey1};
`;

interface OwnProps {
  containerStyle?: ViewStyle | ViewStyle[];
  iconStyle?: ImageStyle | ImageStyle[];
  inputContainerStyle?: ViewStyle | ViewStyle[];
  placeholder?: string;
  onSearchTextChange?: (text: string) => void;
  autoFocus?: boolean;
  value?: string;
  onFocus?: () => void;
  onEndEditing?: () => void;
  rightComponent?: React.ReactElement;
  isOnModal?: boolean;
  setTexting?: Function;
  isTexting?: boolean;
}

type Props = OwnProps;

const SDarkIcon = styled.Image`
  width: 24px;
  height: 24px;
  tint-color: ${props => props.theme.grey2};
`;

const SSectionHeaderView = styled(Animated.View)`
  flex-direction: row;
  align-items: center;
  border-radius: 8px;
  padding: 8px 12px;
  background-color: ${p=>p.theme.neutral8};
  border-width: 1px;
  border-style: solid;
`;

const IconClose = styled.Image`
  width: 24px;
  height: 24px;
  tint-color: ${p=>p.theme.neutral3};
`;

const ViewIcon = styled.View`
  background-color: ${props => props.theme.grey3};
`;


const DynamicTextInput = ({ isOnModal, ...props }:any) => {
  if (isOnModal) {
    return <ModalGrayPlaceholder {...props} />;
  } else {
    return <GrayPlaceholder {...props} />;
  }
};
export const MySearchBar = memoForwardRef((props: Props,ref) => {
  const [text, setText] = useRefState(props.value || '');
  const [, forceUpdate] = useState(false);
  const inputRef = useRef<TextInput>(null);
  const theme = useTheme();

  const isFocus = useSharedValue(props.autoFocus)

  useEffect(() => {
    if (props.autoFocus && props.isOnModal) {
      interactManager(() => {
        inputRef?.current?.focus();
      }, 300);
    }
  }, [props.autoFocus, props.isOnModal]);

  const onSearchTextChange = useCallback(
    _.debounce((_text: string) => {
      if (props.onSearchTextChange) {
        props.onSearchTextChange(_text.toLowerCase());
      }
    }, 300),
    [],
  );

  const animatedStyle = useAnimatedStyle(() => {
    return {
      borderColor: isFocus.value ? theme.primaryColor : theme.neutral4,
    };
  })

  const onChangeText = useCallback(
    (_text: string) => {
      forceUpdate(p => !p);
      if (props.value) {
        return onSearchTextChange(_text);
      }

      if (!props?.isTexting) {
        props.setTexting && props.setTexting(true);
      }

      setText(_text);
      onSearchTextChange(_text);
    },
    [onSearchTextChange, props, setText],
  );

  const onClearText = () => {
    onChangeText('');
  };


  const placeholder = useMemo(
    () => (props.placeholder ? props.placeholder : 'Search'),
    [props.placeholder],
  );

  const onFocus = useWorkletCallback(()=>{
    isFocus.value = true
  },[])

  const onBlur = useWorkletCallback(()=>{
    isFocus.value = false
  },[])

  return (
    <SSectionHeaderView style={[flatStyles(props.containerStyle),animatedStyle]}>
      <SDarkIcon
        style={flatStyles(styles.searchIcon, props.iconStyle)}
        source={IC_OUTLINED_SEARCH}
      />
      <DynamicTextInput
        ref={inputRef}
        keyboardType={Platform.OS === 'android' ? 'default' : 'ascii-capable'}
        style={flatStyles(styles.input, props.inputContainerStyle)}
        placeholder={placeholder}
        value={text}
        onChangeText={onChangeText}
        isOnModal={props.isOnModal}
        autoCorrect={false}
        onFocus={onFocus}
        onBlur={onBlur}
        autoFocus={props.isOnModal ? false : props.autoFocus}
        onEndEditing={props.onEndEditing}
      />
      {text && !props.rightComponent ? (
        <TouchableOpacity onPress={onClearText} style={styles.btnClear}>
            <IconClose style={styles.iconClear} source={IC_FILLED_CANCEL} />
        </TouchableOpacity>
      ) : null}
      {props.rightComponent}
    </SSectionHeaderView>
  );
});

const styles = StyleSheet.create({
  searchIcon: {
    width: 24,
    height: 24,
    resizeMode: 'contain',
  },
  input: {
    flex: 1,
    fontSize: 16,
    height: '100%',
    paddingBottom: 0,
    paddingTop: 0,
    paddingLeft: 10,
    color: Colors.neutral1,
    fontWeight: '400',
  },
  btnClear: {
    alignItems: 'center',
    justifyContent: 'center',
  },
  iconClear: {
    width: 20,
    height: 20,
  },
});
