import {memo, useCallback, useMemo} from 'react';
import {FlexCenter} from '@/utils/helper';
import {styled} from '@true/core';
import {Image, Keyboard, Linking, TouchableOpacity, View} from 'react-native';
import { IC_OUTLINED_DESCRIPTION} from '@true/asset';
import {globalStyles} from '@/utils/globalStyle';
import {IAttachmentFile} from '@/components/AttachmentFile/type';
import {File, Fonts} from '@true/core';
import moment, {Moment} from 'moment';
const FILE_EXTENSION_MATCH = /\.([a-z0-9]+)$/i;
interface AttachmentProps {
  attachment: IAttachmentFile;
  time: number;
}
export const AttachmentFile = memo(function (props: AttachmentProps) {
  const {attachment, time} = props;
  const onPressDownload = useCallback(() => {
    if (attachment.src && /\.(doc|docx|xls|xlsx)$/i.test(attachment.src)) {
      Linking.openURL(
        `https://view.officeapps.live.com/op/embed.aspx?src=${attachment.src}`,
      ).then();
    } else {
      File.download({url: attachment.download});
    }
    Keyboard.dismiss();
  }, [attachment]);
  const filename = attachment?.name || '';

  const extension = useMemo(() => {
    const extensionTest = filename.match(FILE_EXTENSION_MATCH);
    if (!extensionTest || !extensionTest[1]) return null;
    return extensionTest[1];
  }, [filename]);
  return (
    <SContainer onPress={onPressDownload}>
      <FlexCenter>
        <SIcon>
          <Image source={File.getImageFile({ext: extension})}/>
        </SIcon>
        <View style={globalStyles.ml4}>
          <STextAttachments>{filename}</STextAttachments>
          <StyledAttachmentTime>
            {[
              extension?.toUpperCase(),
              attachment.size ? attachment.size + ' KB' : undefined,
              moment(time).format('HH:mm'),
            ]
              .filter(Boolean)
              .join('  ')}
          </StyledAttachmentTime>
        </View>
      </FlexCenter>
    </SContainer>
  );
});
const SContainer = styled.TouchableOpacity`
  margin: 16px 16px 16px 0;
  max-width: 144px;
`;
const SIcon = styled.View`
  align-items: center;
  justify-content: center;
  width: 16px;
  height: 16px;
`;
const STextAttachments = styled.Text`
  font-family: ${Fonts.Regular};
  
  font-weight: 400;
  font-size: 12px;
  color: #666666;
`;

const StyledAttachmentTime = styled(STextAttachments)`
  font-size: 10px;
  color: ${p => p.theme.grey3};
`;
