import React, {memo, useCallback, useRef, useState} from 'react';
import {Fonts, styled, useMe} from '@true/core';
import {Avatar, UIButton} from '@true/ui';
import {getChannels, useChannelsByQuery} from '@/store/channels';
import {MenuItem} from '@/components/MenuItem';
import {BottomSheetModal} from '@gorhom/bottom-sheet';
import {BottomSheetMenu} from '@/components/BottomSheets';
import {
  runOnJS,
  SharedValue,
  useAnimatedReaction,
} from 'react-native-reanimated';
import {IC_OUTLINED_BACKWARD} from '@true/asset';
import {useStyledUpdate} from '@/screens/CreateStyledUpdateScreen/provider';

interface Props {}

export const AvatarWithSelectChannel = memo(({}: Props) => {
  const {selectedChannelId} = useStyledUpdate();
  const me = useMe();
  const bottomSheetRef = useRef<BottomSheetModal | null>(null);
  const channelsOption = useChannelsByQuery('joined').map(id => ({
    label: getChannels(id)?.name || 'Undefined channel',
    value: id,
  }));
  const [label, setLabel] = useState('');

  const onSetLabel = useCallback((_id: number) => {
    setLabel(getChannels(_id)?.name || 'Undefined channel');
  }, []);

  useAnimatedReaction(
    () => selectedChannelId.value,
    cur => {
      if (cur) {
        runOnJS(onSetLabel)(cur);
      }
    },
    [onSetLabel],
  );

  const renderItem = useCallback(
    (item: any, index: number) => {
      return (
        <MenuItem
          key={index}
          title={item.label}
          onPress={() => {
            selectedChannelId.value = item.value;
            // setLabel(item.label);
            bottomSheetRef.current?.dismiss();
          }}
        />
      );
    },
    [bottomSheetRef],
  );

  return (
    <SWrapper>
      <Avatar userKey={me?.id || ''} size={48} />
      <SRight>
        <STitle>{me?.name} shared an update</STitle>
        <SButtonSelect
          title={label ? label : 'Select channel'}
          rightSource={IC_OUTLINED_BACKWARD}
          onPress={() => {
            bottomSheetRef.current?.present();
          }}
        />
      </SRight>
      <BottomSheetMenu
        name={'channel'}
        ref={bottomSheetRef}
        items={channelsOption}
        renderMenuItem={renderItem}
        titleHeader={'Select channel'}
      />
    </SWrapper>
  );
});

const SWrapper = styled.View`
  padding: 12px 16px;
  flex-direction: row;
  background-color: ${p => p.theme.backgroundColor};
`;

const SRight = styled.View`
  margin-left: 12px;
`;

const STitle = styled.Text`
  font-size: 14px;
  line-height: 20px;
  font-family: ${Fonts.Medium};
  color: ${p => p.theme.neutral1};
`;

const SButtonSelect = styled(UIButton).attrs(p => ({
  rightImageSize: 14,
  rightImageStyle: {
    tintColor: p.theme.neutral01,
    transform: [{rotate: '-90deg'}],
    alignSelf: 'center',
    marginRight: 16,
  },
  textStyle: {
    paddingVertical: 6,
    marginRight: 32,
    marginLeft: 20,
    color: p.theme.neutral2,
  },
}))`
  border-radius: 40px;
  border-width: 1px;
  border-color: ${p => p.theme.neutral5};
  justify-content: space-between;
  align-items: center;
  margin-top: 4px;
`;
