import {FileType, memoForwardRef, styled} from '@true/core';
import React, {RefObject, useCallback, useState} from 'react';
import {
  BottomSheetBackdrop,
  BottomSheetBackdropProps,
  BottomSheetModal,
  BottomSheetScrollView,
  BottomSheetView,
} from '@gorhom/bottom-sheet';
import {
  MyBottomSheet,
  useAnimatedSnapPoints,
  AnimatedBottomSpace,
} from '@true/ui';
import BottomSheetHeader from '@/components/BottomSheets/BottomSheetHeader';
import Animated from 'react-native-reanimated';
import {Text, ViewStyle} from 'react-native';
import {CameraRollPicker} from '@true/ui/src/components/CameraRollPicker';
import {useStyledUpdate} from '@/screens/CreateStyledUpdateScreen/provider';

interface Props {
  snapPoints?: Array<string | number>;
  name: string;
  titleHeader: string;
  renderItem?: (item: any, index: number) => JSX.Element;
  items?: any[];
  contentContainerStyle?: ViewStyle;
}

export const BottomSheetStyledUpdate = memoForwardRef(
  (
    {
      snapPoints,
      name,
      titleHeader,
      items,
      renderItem,
      contentContainerStyle,
    }: Props,
    ref: RefObject<BottomSheetModal>,
  ) => {
    const {animatedSnapPoints, onViewLayout} = useAnimatedSnapPoints();
    const {image, cameraRollRef} = useStyledUpdate();

    const onSelected = useCallback(
      (_image: FileType) => {
        if (_image) {
          image.type.value = 'image';
          image.background.value = _image.uri;
        }
      },
      [image],
    );

    const renderBackDrop = useCallback((_props: BottomSheetBackdropProps) => {
      return (
        <BottomSheetBackdrop
          {..._props}
          onPress={() => {
            ref.current?.dismiss();
          }}
          appearsOnIndex={0}
          disappearsOnIndex={-1}
          opacity={0}
        />
      );
    }, []);

    return (
      <SMyBottomSheet
        ref={ref}
        name={name}
        snapPoints={snapPoints || animatedSnapPoints}
        enableContentPanningGesture={true}
        backdropComponent={renderBackDrop}>
        {titleHeader ? (
          <BottomSheetHeader
            title={titleHeader}
            onPress={() => {
              ref?.current?.dismiss();
            }}
          />
        ) : null}

        {items && renderItem ? (
          <BottomSheetScrollView
            showsVerticalScrollIndicator
            contentContainerStyle={contentContainerStyle}>
            <SContainer>
              {items.map((item, index) => (
                <>{renderItem(item, index)}</>
              ))}
            </SContainer>
            <AnimatedBottomSpace />
          </BottomSheetScrollView>
        ) : (
          <CameraRollPicker
            ref={cameraRollRef}
            isBottomSheet={true}
            onSelectImage={onSelected}
            selected={[]}
            selectSingleItem={true}

            // maxFiles={1}
          />
        )}
        {/*</BottomSheetView>*/}
      </SMyBottomSheet>
    );
  },
);

const SContainer = styled.View`
  flex-direction: row;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
  padding-top: 16px;
`;

const SMyBottomSheet = styled(MyBottomSheet)`
  box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.25);
`;
