import {memo} from 'react';
import {Fonts} from '@true/core';
import {styled} from '@true/core';
import {UIText} from '@true/ui';

interface CreateButtonProps {
  onPress: () => void;
  text?: string;
}

export const CreateButton = memo(function (props: CreateButtonProps) {
  const {onPress, text} = props;
  return (
    <SButtonCreate onPress={onPress}>
      <STextCreate>{text ? text : 'Create'}</STextCreate>
    </SButtonCreate>
  );
});
const SButtonCreate = styled.TouchableOpacity`
  flex-direction: row;
  align-items: center;
  justify-content: center;
  padding: 4px 16px;
  width: 94px;
  background-color: ${p => p.theme.primaryColor};
  border-radius: 8px;
`;
const STextCreate = styled(UIText.ButtonMedium)`
  color: #ffffff;
`;
