import {memo, useCallback, useEffect, useMemo, useState} from 'react';
import {styled, useMe} from '@true/core';
import {navigateToDetailPostScreen} from '@/utils';
import {useUpdates} from '@/store/updates';
import {ItemPollOption} from '@/components/PostThumbnail/PollPost/ItemPollOption';
import {IOptionPoll, IVotedPoll} from '@/store/updates/type';
import {requestVoted} from '@/store/updates/function';
import {useAsyncFn} from '@/hooks/useAsyncFn';
import SimpleToast from 'react-native-simple-toast/index';
import {toBoolean} from '@/components/CameraRollPicker/Row';
import {PollContent} from '@/components/PostThumbnail/PollPost/PollContent';
import {ListHeaderPollOptions} from '@/components/PostThumbnail/PollPost/ListHeaderPollOptions';
import {ListFooterPollOptions} from '@/components/PostThumbnail/PollPost/ListFooterPollOptions';
import {useSharedValue} from 'react-native-reanimated';
import {useShouldShowSubmitButton} from '@/components/PostThumbnail/PollPost/hook';

interface PollPostProps {
  id: number;
  isInFeed: boolean;
}

export const PollPost = memo(function (props: PollPostProps) {
  const {id, isInFeed} = props;
  const _data = useUpdates(id);
  const dataPoll = _data?.attachment;
  const config: any = dataPoll.config || {};
  const me = useMe();
  const isRevoting = useSharedValue(false);

  const {votesNumber, youVoted, usersByVotes, listMyVoted} = useMemo(() => {
    const votes = Object.values(dataPoll.cached_votes);
    const _config: any = dataPoll.config || {};

    const youVoted = !!dataPoll.cached_votes['u' + me?.id];
    const listMyVoted = dataPoll.cached_votes['u' + me?.id]?.votes || [];
    return {
      votesNumber: votes.length,
      youVoted,

      // voteId => userId[]
      usersByVotes: votes.reduce<Record<string, IVotedPoll[]>>(
        (prev: any, curr: any) => {
          curr.votes.forEach((vote: any) => {
            if (!prev[vote]) prev[vote] = [];
            prev[vote].push(curr);
          });
          return prev;
        },
        {},
      ),
      // duration: (() => {
      //   const start = _config.has_stime
      //     ? moment(_config.stime, 'X').format('HH:mm MMM DD')
      //     : undefined;
      //
      //   const end = _config.has_etime
      //     ? moment(_config.etime, 'X').format('HH:mm MMM DD')
      //     : undefined;
      //
      //   if (start && !end) {
      //     return `Start at  ** ${start} '**'`;
      //   }
      //
      //   if (!start && end) {
      //     return `End at  ** ${start} '**'`;
      //   }
      //
      //   if (!start && !end) return '';
      //
      //   return `Poll duration **${start} - ${end}**`;
      // })(),
      // created: moment(dataPoll.since, 'X').format('HH:mm DD/MM/YYYY'),
      // userName: getUser(dataPoll.user_id)?.name,
      listMyVoted,
    };
  }, [id, _data]);

  const dataList = useMemo(
    () =>
      isInFeed ? dataPoll.cached_options.slice(0, 3) : dataPoll.cached_options,
    [isInFeed, dataPoll.cached_options],
  );

  const shouldShowSubmitButton = useShouldShowSubmitButton(config, youVoted);
  const [selectedVoteIds, setSelectedVoteIds] = useState(
    new Set(dataPoll.cached_votes['u' + me?.id]?.votes || []) || {},
  );

  const onOptionPress = useCallback(
    (option: IOptionPoll) => {
      setSelectedVoteIds(set => {
        if (!config?.multi) {
          const newSet = new Set<string>();
          newSet.add(option.id);
          return newSet;
        }

        if (config?.has_limit && config?.limited_answers) {
          if (!set.has(option.id) && set.size >= config?.limited_answers)
            return set;
        }

        const newSet = new Set(set);
        newSet.has(option.id)
          ? newSet.delete(option.id)
          : newSet.add(option.id);
        return newSet;
      });
    },
    [config],
  );
  const [{loading: voting, error: votingError}, onPressVote] =
    useAsyncFn(async () => {
      await requestVoted({
        id: dataPoll.id,
        answers: [...selectedVoteIds].join(','),
      });
      SimpleToast.show('Submit vote successfully');
    }, [id, selectedVoteIds, dataPoll]);

  useEffect(() => {
    if (!votingError) {
      return;
    }
    let _mess = votingError.message;
    if (`${_mess}` === '998') {
      _mess = 'Select at least one choice';
    }
    SimpleToast.show(_mess);
  }, [votingError]);

  const onNavigateToDetail = useCallback(() => {
    navigateToDetailPostScreen({
      id: id,
      user_id: _data?.user_id || 0,
      time: _data?.since || 0,
      hid: _data?.hid || '',
    });
  }, []);

  return (
    <SContainer>
      <PollContent id={id} isInFeed={isInFeed} />
      <SVotesWrapper>
        <ListHeaderPollOptions
          id={id}
          votesNumber={votesNumber}
          youVoted={youVoted}
        />
        {dataList.map((item: any, index: number) => (
          <ItemPollOption
            option={item}
            key={index}
            onPress={onOptionPress}
            checked={selectedVoteIds.has(item.id)}
            // selectedVoteIds={selectedVoteIds}
            votes={usersByVotes[item.id]}
            totalVote={votesNumber}
            multi={toBoolean(config.multi) ? 1 : 0}
            mineVoted={listMyVoted.includes(item.id)}
            youVoted={youVoted}
            shouldShowSubmitButton={shouldShowSubmitButton}
            isRevoting={isRevoting}
          />
        ))}
        <ListFooterPollOptions
          id={id}
          isInFeed={isInFeed}
          onNavigateToDetail={onNavigateToDetail}
          onSubmit={onPressVote}
          shouldShowSubmitButton={shouldShowSubmitButton}
          isRevoting={isRevoting}
          youVoted={youVoted}
          voting={voting}
        />
      </SVotesWrapper>
    </SContainer>
  );
});

const SContainer = styled.View``;
const SVotesWrapper = styled.View`
  margin: 4px 0;
  width: 100%;
`;
const keyExtractor = (item: any) => `${item.id.toString()}+poll`;
