import {memo, useCallback} from 'react';
import {SharedValue, useDerivedValue} from 'react-native-reanimated';
import {styled} from '@true/core';
import {UIButton} from '@true/ui';
import {UICheckbox} from '@/components/UICheckbox';

interface Props {
  params: SharedValue<any>;
  keyName: string;
  title: string;
}

export const CheckboxWithText = memo(({params, keyName, title}: Props) => {
  const animatedCheckbox = useDerivedValue(
    () => !!params.value[keyName],
    [keyName],
  );

  const onTick = useCallback(() => {
    const value = animatedCheckbox.value ? 0 : 1;

    params.value = {
      ...params.value,
      [keyName]: value,
    };
  }, [animatedCheckbox, keyName, params]);

  return (
    <SBtn onPress={onTick}>
      <STitleView>
        <UICheckbox value={animatedCheckbox} />
      </STitleView>
      <STitleCheckbox>{title}</STitleCheckbox>
    </SBtn>
  );
});

const SBtn = styled(UIButton)`
  flex: 1;
  flex-direction: row;
  margin-bottom: 12px;
`;

const STitleView = styled.View`
  max-height: 24px;
  justify-content: center;
`;

const STitleCheckbox = styled.Text`
  font-size: 14px;
  color: ${p => p.theme.neutral1};
  padding-left: 12px;
`;
