import React, {memo} from 'react';
import {TouchableOpacityProps} from 'react-native';
import styled from 'styled-components/native';

export const MenuItem = memo(
  ({
    source,
    title,
    hideDivider,
    ...rest
  }: {
    source?: any;
    title: string;
    hideDivider?: boolean;
  } & TouchableOpacityProps) => {
    return (
      // @ts-ignore
      <SMenuContainer {...rest}>
        <SMenuWrapper>
          {source ? <SMenuIcon source={source} /> : null}
          <SMenuTitle>{title}</SMenuTitle>
        </SMenuWrapper>
        {hideDivider ? null : <SMenuDivider />}
      </SMenuContainer>
    );
  },
);

const SMenuContainer = styled.TouchableOpacity``;

const SMenuWrapper = styled.View`
  flex-direction: row;
  align-items: center;
  padding: 16px;
`;
const SMenuIcon = styled.Image`
  margin-right: 12px;
`;

const SMenuTitle = styled.Text`
  color: ${p => p.theme.grey1};
`;
const SMenuDivider = styled.View`
  height: 1px;
  background-color: ${p => p.theme.neutral05};
  margin: 0 16px;
`;
