import {Core, Fetch} from '@true/core';
import {batch} from 'react-redux';
import {ICreateLetter, ILetter} from '@/store/letters/type';
import {setLettersQuery, syncLetters} from '@/store/letters/index';

export const requestLetters = async () => {
  const {data} = await Fetch.postWithToken<{
    letters: ILetter[];
  }>(`@${Core.systemPath}/inside/ajax/api/company/letter/gets`);

  batch(() => {
    if (data.letters.length) {
      syncLetters(data.letters);
      setLettersQuery(prev => ({
        ...prev,
        ['all']: data.letters.map(item => item.id.toString()),
      }));
    }
  });
  return data.letters;
};
export const requestLetterId = async (params: {letter_id: string | number}) => {
  const {data} = await Fetch.postWithToken<{
    letter: ILetter;
  }>(
    `@${Core.systemPath}/inside/ajax/api/company/letter/get?letter_id=${params.letter_id}`,
  );

  batch(() => {
    if (data.letter) {
      syncLetters([data.letter]);
    }
  });
  return data.letter;
};
