import React, {memo} from 'react';
import Animated, {
  useAnimatedStyle,
  useSharedValue,
  useWorkletCallback,
  withTiming,
} from 'react-native-reanimated';
import {useAnimatedSafeAreaInsets} from 'react-native-safe-area-context';
import {goBack, styled} from '@true/core';
import {IC_OUTLINED_LEFT, IC_OUTLINED_SEARCH} from '@true/asset';
import {CreateButton} from '@/components/CreateButton';
import {FlexCenter} from '@/utils/helper';
import {UIButton, UIText} from '@true/ui';
import Metrics from '@/assets/Metrics';
import {MySearchBar} from '@/screens/SearchScreen/SearchBar';
import {View} from 'react-native';

interface HeaderProps {
  title: string;
  isSearch: boolean;
  onCreate?: () => void;
  onSearch?: () => void;
  isBack?: boolean;
  isDivider?: boolean;
  okText?: string;
  onChangeText?: (q: string) => void;
}

export const CustomHeader = memo(function (props: HeaderProps) {
  const {
    title,
    isSearch,
    onCreate,
    onSearch,
    isBack,
    isDivider,
    okText,
    onChangeText,
  } = props;
  const {aTop} = useAnimatedSafeAreaInsets();
  const isShow = useSharedValue(false);
  const animatedPaddingTop = useAnimatedStyle(() => ({
    paddingTop: aTop.value,
    borderBottomColor: isDivider ? '#DDDDE3' : 'transparent',
    borderBottomWidth: isDivider ? 1 : 0,
    backgroundColor: 'white',
  }));
  const animatedStyle = useAnimatedStyle(() => ({
    opacity: isShow.value ? 0 : 1,
  }));
  const animatedSearchStyle = useAnimatedStyle(() => ({
    //44= 16 +16 +12 + 16
    width: withTiming(isShow.value ? Metrics.screen.width - 60 : 0),
    opacity: withTiming(isShow.value ? 1 : 0),
    display: isShow.value ? 'flex' : 'none',
  }));
  const toggleSearchBox = useWorkletCallback(() => {
    isShow.value = !isShow.value;
  }, []);
  return (
    <Animated.View style={animatedPaddingTop}>
      <View>
        <SSearchView style={animatedSearchStyle}>
          <MySearchBar
            containerStyle={{
              backgroundColor: 'white',
              width: '100%',
            }}
            onSearchTextChange={onChangeText}
          />
          <SCancelButton onPress={toggleSearchBox} />
        </SSearchView>
        <SContentContainer style={animatedStyle}>
          <FlexCenter gap={8}>
            {isBack ? (
              <UIButton source={IC_OUTLINED_LEFT} onPress={goBack} />
            ) : null}
            <STextHeading>{title}</STextHeading>
          </FlexCenter>
          <FlexCenter gap={8}>
            {isSearch && (
              <UIButton
                onPressIn={toggleSearchBox}
                source={IC_OUTLINED_SEARCH}
                onPress={onSearch && onSearch}
              />
            )}
            {onCreate && <CreateButton onPress={onCreate} text={okText} />}
          </FlexCenter>
        </SContentContainer>
      </View>
    </Animated.View>
  );
});

const SContentContainer = styled(Animated.View)`
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  padding: 10px 16px;
`;
const STextHeading = styled(UIText.HeaderLarge)`
  color: ${p => p.theme.neutral1};
`;
const SSearchView = styled(Animated.View)`
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  padding: 0 16px;
  border-radius: 8px;
  flex-direction: row;
  background-color: white;
  justify-content: flex-end;
  height: 80%;
  z-index: 10;
  margin-top: 4px;
`;
const SCancelButton = styled(UIButton).attrs(p => ({
  title: 'Cancel',
  textStyle: {
    color: p.theme.neutral2,
  },
}))`
  align-items: center;
  justify-content: center;
  margin-left: 12px;
`;
