import React, {memo, useCallback, useState} from 'react';
import {Fonts, styled} from '@true/core';
import Animated, {
  runOnJS,
  useAnimatedProps,
  useAnimatedReaction,
  useAnimatedStyle,
} from 'react-native-reanimated';
import {Dimensions, StyleSheet, TextInput} from 'react-native';
import {hexToRgbA} from '@/utils';
import {useStyledUpdate} from '@/screens/CreateStyledUpdateScreen/provider';
import {getTemplateImage} from '@/services/BgTemplateService';

const AnimatedTextInput = Animated.createAnimatedComponent(TextInput);
const {width: DWidth} = Dimensions.get('screen');

const ContentBackground = memo(() => {
  const {image, selectedOpacity} = useStyledUpdate();
  const [isTemplate, setIsTemplate] = useState(false);
  const [uri, setUri] = useState('');

  useAnimatedReaction(
    () => image.type.value,
    cur => {
      runOnJS(setIsTemplate)(cur === 'template');
    },
    [setIsTemplate],
  );

  useAnimatedReaction(
    () => image.background.value,
    cur => {
      runOnJS(setUri)(cur);
    },
    [setUri],
  );

  const renderImage = useCallback(() => {
    if (uri === '') return null;

    if (isTemplate) {
      return (
        <SFastImage
          source={getTemplateImage(uri as any)}
          resizeMode={'stretch'}
        />
      );
    }

    return <SFastImage source={{uri}} resizeMode={'stretch'} />;
  }, [uri, isTemplate]);

  const animatedStyle = useAnimatedStyle(() => ({
    opacity: selectedOpacity.value,
  }));

  return (
    <Animated.View style={[StyleSheet.absoluteFillObject, animatedStyle]}>
      {renderImage()}
    </Animated.View>
  );
});

interface Props {}

export const ContentUpdate = memo(({}: Props) => {
  const {selectedColor, content} = useStyledUpdate();

  const onChangeText = useCallback(
    (text: string) => {
      content.value = text;
    },
    [content],
  );

  const animatedProps = useAnimatedProps(
    () => ({
      text: content.value.toString(),
    }),
    [],
  ) as any;

  const animatedStyle = useAnimatedStyle(() => ({
    backgroundColor: selectedColor.value,
  }));

  return (
    <SWrapper style={animatedStyle}>
      <ContentBackground />
      <STextInput
        {...{animatedProps}}
        multiline={true}
        selectionColor={'white'}
        textAlign={'left'}
        onChangeText={onChangeText}
      />
      {/*<SPlaceHolder>What's on your mind?</SPlaceHolder>*/}
    </SWrapper>
  );
});

const SWrapper = styled(Animated.View)`
  width: 100%;
  height: 216px;
  align-items: center;
  justify-content: center;
  padding: 16px;
`;

const SPlaceHolder = styled.Text`
  position: absolute;
  font-size: 24px;
  line-height: 32px;
  font-family: ${Fonts.Bold};
  color: ${p => p.theme.white};
  opacity: 0.6;
  z-index: -1;
`;

const STextInput = styled(AnimatedTextInput).attrs(p => ({
  placeholder: "What's on your mind?",
  placeholderTextColor: hexToRgbA(p.theme.white, 0.6),
}))`
  font-size: 24px;
  line-height: 32px;
  font-family: ${Fonts.Bold};
  color: ${p => p.theme.white};
`;

const SFastImage = styled.Image`
  width: 100%;
  height: 100%;
`;
