import {ToastService} from '@true/core';
import {useEffect} from 'react';

export const useAutoToastError = (
  error: Error | undefined,
  action?: string,
  defaultMessage?: string,
) => {
  useEffect(() => {
    if (!error) return;
    ToastService.show(error.message);
  }, [error, defaultMessage]);
};

export const useAutoToastErrorWithGravity = (
  error: Error | undefined,
  gravity: 'top' | 'bottom',
  action?: string,
  defaultMessage?: string,
) => {
  useEffect(() => {
    if (!error) return;
    ToastService.showWithGravity(error.message, gravity);
  }, [error, defaultMessage]);
};

export default useAutoToastError;
